import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function CCStructure() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Structure
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Structure</h1>

                            <p>A structure is a user-defined data type. A structure creates a data type that can be used to group items of possibly different types into a single type.</p>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`struct structureName{
    member1;
    member2;
    member3;
    .
    .
    .
    memberN;
};`}

                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>

                                    {`
#include<iostream.h> 
#include<conio.h> 	
struct Rectangle      
{      
   int width, height;      
      
};      
int main(void) {    
    struct Rectangle rec;    
    rec.width=8;    
    rec.height=5;    
    cout<<"Area of Rectangle is: "<<(rec.width * rec.height)<<endl;    
    return 0;    
}

Output:

Area of Rectangle is: 40`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Structure v/s Class</h1>
                            <table style={{ border: "2px solid skyblue", width: "100%" }}>
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        <th style={{ border: "1px solid skyblue", padding: "5px 0px 5px 10px" }}>Structure</th>
                                        <th style={{ border: "1px solid skyblue", padding: "5px 0px 5px 10px" }}>Class</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            If access specifier is not declared explicitly, then by default
                                            access specifier will be <b>public</b>.
                                        </td>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            If access specifier is not declared explicitly, then by default
                                            access specifier will be <b>private</b>.
                                        </td>
                                    </tr>

                                    <tr>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            Syntax of Structure:
                                            <br />
                                            <br />
                                            struct structure_name
                                            <br />
                                            {"{"}
                                            <br />
              {`// body of the structure. `}
                                            <br />
                                            {"}"}
                                        </td>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            Syntax of Class:
                                            <br />
                                            <br />
                                            class class_name
                                            <br />
                                            {"{"}
                                            <br />
                                            {`// body of the class.`}
                                            <br />
                                            {"}"}
                                        </td>
                                    </tr>

                                    <tr>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            The instance of the structure is known as{" "}
                                            <b>"Structure variable"</b>.
                                        </td>
                                        <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
                                            The instance of the class is known as <b>"Object of the class"</b>.
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
