import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Datatype() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        DataType
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Data Type</h1>

                            <p>A data type specifies the type of data that a variable can store such as integer, floating, character, etc.</p>
                            <h4 className="mb-2 mt-3">Data Types in C</h4>
                            <ol>
                                <li>Basic</li>
                                <li>Derived</li>
                                <li>Enumeration</li>
                                <li>Void</li>
                            </ol>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                There are the following data types in C language.
                            </h3>
                            <table
  style={{
    border: "2px solid skyblue",
    borderCollapse: "collapse",
    width: "100%",
  }}
>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Types</th>
      <th style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Data Types</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>Basic Data Type</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>int, char, float, double</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>Derived Data Type</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>array, pointer, structure, union</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>Enumeration Data Type</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>enum</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>Void Data Type</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>void</td>
    </tr>
  </tbody>
</table>

                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Data types Memory size and Range in C language.
                            </h3>
                            <table
  style={{
    border: "2px solid skyblue",
    borderCollapse: "collapse",
    width: "100%",
  }}
>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Data Types</th>
      <th style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Memory Size</th>
      <th style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Range</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>char</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>−128 to 127</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>signed char</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>−128 to 127</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>unsigned char</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>0 to 255</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>short</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>2 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>−32,768 to 32,767</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>unsigned short</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>2 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>0 to 65,535</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>int</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>2 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>−32,768 to 32,767</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>unsigned int</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>2 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>0 to 65,535</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>long int</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>4 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>−2,147,483,648 to 2,147,483,647</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>unsigned long int</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>4 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>0 to 4,294,967,295</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>float</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>4 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1E–37 to 1E+37 (6 digits precision)</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>double</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>8 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1E–37 to 1E+37 (10 digits precision)</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>long double</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>10 byte</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 10px" }}>1E–37 to 1E+37 (10 digits precision)</td>
    </tr>
  </tbody>
</table>

                            <br />
                            <figure className="code-block">
                                <pre>
                                <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                                    {`#include<stdio.h>
#include<conio.h>
#include<stdlib.h>
#include<limits.h>
#include<float.h>
void main(int argc, char** argv) {
  printf("CHAR_BIT    :   %d\\n", CHAR_BIT);
  printf("CHAR_MAX    :   %d\\n", CHAR_MAX);
  printf("CHAR_MIN    :   %d\\n", CHAR_MIN);
  printf("INT_MAX     :   %d\\n", INT_MAX);
  printf("INT_MIN     :   %d\\n", INT_MIN);
  printf("LONG_MAX    :   %ld\\n", (long) LONG_MAX);
  printf("LONG_MIN    :   %ld\\n", (long) LONG_MIN);
  printf("SCHAR_MAX   :   %d\\n", SCHAR_MAX);
  printf("SCHAR_MIN   :   %d\\n", SCHAR_MIN);
  printf("SHRT_MAX    :   %d\\n", SHRT_MAX);
  printf("SHRT_MIN    :   %d\\n", SHRT_MIN);
  printf("UCHAR_MAX   :   %d\\n", UCHAR_MAX);
  printf("UINT_MAX    :   %u\\n", (unsigned int) UINT_MAX);
  printf("ULONG_MAX   :   %lu\\n", (unsigned long) ULONG_MAX);
  printf("USHRT_MAX   :   %d\\n", (unsigned short) USHRT_MAX);
  printf("Storage size for float : %d \\n", sizeof(float));
  printf("FLT_MAX     :   %g\\n", (float) FLT_MAX);
  printf("FLT_MIN     :   %g\\n", (float) FLT_MIN);
  printf("-FLT_MAX    :   %g\\n", (float) -FLT_MAX);
  printf("-FLT_MIN    :   %g\\n", (float) -FLT_MIN);
  printf("DBL_MAX     :   %g\\n", (double) DBL_MAX);
  printf("DBL_MIN     :   %g\\n", (double) DBL_MIN);
  printf("-DBL_MAX     :  %g\\n", (double) -DBL_MAX);
  printf("Precision value: %d\\n", FLT_DIG );
  getch();
}

Output:

CHAR_BIT    :   8
CHAR_MAX    :   127
CHAR_MIN    :   -128
INT_MAX     :   2147483647
INT_MIN     :   -2147483648
LONG_MAX    :   9223372036854775807
LONG_MIN    :   -9223372036854775808
SCHAR_MAX   :   127
SCHAR_MIN   :   -128
SHRT_MAX    :   32767
SHRT_MIN    :   -32768
UCHAR_MAX   :   255
UINT_MAX    :   4294967295
ULONG_MAX   :   18446744073709551615
USHRT_MAX   :   65535
Storage size for float : 4 
FLT_MAX      :   3.40282e+38
FLT_MIN      :   1.17549e-38
-FLT_MAX     :   -3.40282e+38
-FLT_MIN     :   -1.17549e-38
DBL_MAX      :   1.79769e+308
DBL_MIN      :   2.22507e-308
-DBL_MAX     :  -1.79769e+308
Precision value: 6`}

                                </pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">The sizeof() Function</h1>
                            <p>sizeof() function is used to find the memory space allocated for each C data types.</p>
                            <br />
                            <figure className="code-block">
                                <pre>
                                <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                                {`#include<stdio.h>
#include<conio.h>
void main()
{
 int a;
 float b;
 double c;
 char d;
 printf("Size of int=%d bytes\\n",sizeof(a));
 printf("Size of float=%d bytes\\n",sizeof(b));
 printf("Size of double=%d bytes\\n",sizeof(c));
 printf("Size of char=%d byte\\n",sizeof(d));
 getch();
}

Output:

Size of int = 4 bytes
Size of float = 4 bytes
Size of double = 8 bytes
Size of char = 1 byte `}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
