import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Oopspop() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Oops Vs POP
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Oops Vs POP</h1>

                            <p>Differences between Procedural and Object Oriented Programming.</p>
                            <table
                                style={{
                                    border: "2px solid skyblue",
                                    borderCollapse: "collapse",
                                    width: "100%",
                                }}
                            >
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        {["SN", "Object Oriented Programming (OOP)", "Procedural Oriented Programming (POP)"].map((th, i) => (
                                            <th
                                                key={i}
                                                style={{ border: "1px solid skyblue", padding: "5px 10px" }}
                                            >
                                                {th}
                                            </th>
                                        ))}
                                    </tr>
                                </thead>
                                <tbody>
                                    {[
                                        ["1", "In object oriented programming, program is divided into small parts called objects.", "In procedural programming, program is divided into small parts called functions."],
                                        ["2", "Object oriented programming follows bottom up approach.", "Procedural programming follows top down approach."],
                                        ["3", "Object oriented programming have access specifiers like private, public, protected etc.", "There is no access specifier in procedural programming."],
                                        ["4", "Adding new data and function is easy.", "Adding new data and function is not easy."],
                                        ["5", "Object oriented programming provides data hiding so it is more secure.", "Procedural programming does not have any proper way for hiding data so it is less secure."],
                                        ["6", "Overloading is possible in object oriented programming.", "In procedural programming, overloading is not possible."],
                                        ["7", "In object oriented programming, data is more important than function.", "In procedural programming, function is more important than data."],
                                        ["8", "Object oriented programming is based on real world.", "Procedural programming is based on unreal world."],
                                        ["9", "Examples: C++, Java, Python, C# etc.", "Examples: C, FORTRAN, Pascal, Basic etc."],
                                    ].map((row, i) => (
                                        <tr key={i}>
                                            {row.map((cell, j) => (
                                                <td
                                                    key={j}
                                                    style={{ border: "1px solid skyblue", padding: "3px 10px" }}
                                                >
                                                    {cell}
                                                </td>
                                            ))}
                                        </tr>
                                    ))}
                                </tbody>
                            </table>

                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
