import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function FileSystem() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        File System
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">File System</h1>
                            <p>Since PHP is a server side programming language, it allows you to work with files and directories stored on the web server. In this tutorial you will learn how to create, access, and manipulate files on your web server using the PHP file system functions.</p>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Opening a File with PHP fopen() Function</h1>
                            <p>PHP fopen() function is used to open file or URL and returns resource. The fopen() function accepts two arguments: $filename and $mode. The $filename represents the file to be opended and $mode represents the file mode for example read-only, read-write, write-only etc.</p>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`fopen(filename, mode)
                                    `}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$handle = fopen("data.txt", "r");
?>
                                    `}
                                </pre></figure>
                            <p>The file may be opened in one of the following modes:</p>
                            <table style={{ borderCollapse: "collapse", width: "100%", border: "2px solid skyblue" }}>
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>Modes</th>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>What it does</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>r</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Open the file for reading only.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>r+</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Open the file for reading and writing.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>w</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Open the file for writing only and clears the contents of file. If the file does not exist, PHP will attempt to create it.
                                        </td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>w+</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Open the file for reading and writing and clears the contents of file. If the file does not exist, PHP will attempt to create it.
                                        </td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>a</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Append. Opens the file for writing only. Preserves file content by writing to the end of the file. If the file does not exist, PHP will attempt to create it.
                                        </td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>a+</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Read/Append. Opens the file for reading and writing. Preserves file content by writing to the end of the file. If the file does not exist, PHP will attempt to create it.
                                        </td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>x</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Open the file for writing only. Return FALSE and generates an error if the file already exists. If the file does not exist, PHP will attempt to create it.
                                        </td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>x+</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
                                            Open the file for reading and writing; otherwise it has the same behavior as 'x'.
                                        </td>
                                    </tr>
                                </tbody>
                            </table>

                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Read File with PHP fread() Function</h1>
                            <p>PHP provides various functions to read data from file. There are different functions that allow you to read all file data, read data line by line and read data character by character. The PHP fread() function is used to read data of the file. It requires two arguments: file resource and file size.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`string fread (resource $handle , int $length )   `}</pre></figure>
                            <p>$handle represents file pointer that is created by fopen() function.</p>

                            <p>$length represents length of byte to be read.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php    
$filename = "c:\\file1.txt";    
$fp = fopen($filename, "r");//open file in read mode    
  
$contents = fread($fp, filesize($filename));//read file    
  
echo "<pre>$contents</pre>";//printing data of file  
fclose($fp);//close file    
?>
                                    `}
                                </pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">PHP Write File - fwrite()</h1>
                            <p>The PHP fwrite() function is used to write content of the string into file.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`int fwrite ( resource $handle , string $string [, int $length ] )  
                                    `}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$fp = fopen('data.txt', 'w');//opens file in write-only mode  
fwrite($fp, 'welcome ');  
fwrite($fp, 'to php file write');  
fclose($fp);  
  
echo "File written successfully";  
?>
Output:

welcome to php file write`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Append to File</h1>
                            <p>You can append data into file by using a or a+ mode in fopen() function. Let's see a simple example that appends data into data.txt file.</p>
                            <p>Let's see the data of file first.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- data.txt -->`}</div>
                                    {`welcome to php file write`}</pre></figure>
                            <hr className="my-5" />
                            <h3>PHP Append to File - fwrite()</h3>
                            <p>The PHP fwrite() function is used to write and append data into file.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$fp = fopen('data.txt', 'a');//opens file in append mode  
fwrite($fp, ' this is additional text ');  
fwrite($fp, 'appending data');  
fclose($fp);  
  
echo "File appended successfully";  
?>

Output:

welcome to php file write this is additional text appending data`}</pre></figure>
                            <hr className="my-5" />
                            <h3>Delete File</h3>
                            <p>In PHP, we can delete any file using unlink() function. The unlink() function accepts one argument only: file name. It is similar to UNIX C unlink() function.</p>
                            <p>PHP unlink() generates E_WARNING level error if file is not deleted. It returns TRUE if file is deleted successfully otherwise FALSE.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`bool unlink ( string $filename [, resource $context ] )  `}</pre></figure>
                            <p>$filename represents the name of the file to be deleted.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$status=unlink('data.txt');    
if($status){  
echo "File deleted successfully";    
}else{  
echo "Sorry!";    
}  
?>  

Output:

File deleted successfully`}</pre></figure>
                            <hr className="my-5" />
                            <h3>Closing a File with PHP fclose() Function</h3>
                            <p>Once you've finished working with a file, it needs to be closed. The fclose() function is used to close the file, as shown in the following example:</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$file = "data.txt";
 
// Check the existence of file
if(file_exists($file)){
    // Open the file for reading
    $handle = fopen($file, "r") or die("ERROR: Cannot open the file.");
        
    /* Some code to be executed */
        
    // Closing the file handle
    fclose($handle);
} else{
    echo "ERROR: File does not exist.";
}

?>  

Output:

File opened successfully.
Now closing the file.
File closed successfully. `}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
