import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import oops from '../../../Images/data/c++/oops.png';

export default function Oops() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        OOPs
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Object Oriented Programming Language</h1>
                            <p>Object oriented programming is a way of solving complex problems by breaking them into smaller problems using objects. Before Object Oriented Programming (commonly referred as OOP), programs were written in procedural language, they were nothing but a long list of instructions. On the other hand, the OOP is all about creating objects that can interact with each other, this makes it easier to develop programs in OOP as we can understand the relationship between them.</p>
                            <ul>
                                <li>Object</li>
                                <li>Class</li>
                                <li>Inheritance</li>
                                <li>Polymorphism</li>
                                <li>Abstraction</li>
                                <li>Encapsulation</li>
                            </ul>
                            <center><img src={oops} alt="Oops"/></center>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Object
                            </h3>
                            <p>Any entity that has state and behavior is known as an object. It can be physical and logical.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Object Example -->`}</div>
                                    {`A obj;
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Class
                            </h3>
                            <p>A class, on the other hand, is a blueprint of the object. Conversely, an object can be defined as an instance of a class. A class contains a skeleton of the object and does not take any space in the memory.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Class Syntax -->`}</div>
                                    {`Class A{ //
 Public: private: protected:
 Int a;
 Void display();
};`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Object and Class example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Student {
  public:
    int id;//data member (also instance variable)
    string name;//data member(also instance variable)
};
void main() {
  Student s1; //creating an object of Student
  s1.id = 201;
  s1.name = "Rahul";
  cout<<s1.id<<endl;
  cout<<s1.name<<endl;
  getch();
}
   
Output:
  
201
Rahul 
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Inheritance
                            </h3>
                            <p>When one object acquires all the properties and behaviours of parent object i.e. known as inheritance. It provides code reusability. It is used to achieve runtime polymorphism. Inheritance are 5 types</p>
                            <ul>
                                <li>single inheritance</li>
                                <li>multiple inheritance</li>
                                <li>multilevel Inheritance</li>
                                <li>hybrid inheritance</li>
                                <li>hierarchical inheritance</li>
                            </ul>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Polymorphism
                            </h3>
                            <p>When one task is performed by different ways i.e. known as polymorphism. we use Function overloading and Function overriding to achieve polymorphism.</p>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Abstraction
                            </h3>
                            <p>Hiding internal details and showing functionality is known as abstraction. For example: phone call, we don't know the internal processing.</p>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Encapsulation
                            </h3>
                            <p>Binding (or wrapping) code and data together into a single unit is known as encapsulation. For example: capsule, it is wrapped with different medicines.</p>
                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}