import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import ife from '../../../Images/data/c++/if-else.png';
import ifelseif from '../../../Images/data/c++/if-else1.png';
import nestedif from '../../../Images/data/c++/else-if.png';

export default function CCIfelse() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Decision Making
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                    <article className="main-content col-md-9 pr-lg-5">
                        
                    <h1 className="mb-2 mt-3">Decision Making</h1>
						<p>Decision making structures require that the programmer specify one or more conditions to be evaluated or tested by the program, along with a statement or statements to be executed if the condition is determined to be true, and optionally, other statements to be executed if the condition is determined to be false.</p>
						<p>For decision making in C++, we have four types of control statements (or control structures), which are as follows:</p>
						<ol>
							<li>if statement</li>
							<li>nested if statement</li>
							<li>if-else statement</li>
							<li>if-else-if statement</li>
							<li>Switch statement</li>
						</ol>
                        <hr className="my-5"/>
						<h1 className="mb-2 mt-3">If statement</h1>
						<p>The C++ if statement tests the condition. It is executed if condition is true.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition){    
//code to be executed    
}		`}</pre></figure>
<br/>	
						<img src={ife} alt="If"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h> 
#include<conio.h> 
void main() 						
{ 
    int num = 10;    
    if (num % 2 == 0)    
    {    
        cout<<"It is even number";    
    }   
    getch();
}
  
Output:
  
It is even number`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">nested if statement</h1>
						<p>When there is an if statement inside another if statement then it is called the nested if statement. The structure of nested if looks like this:</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition_1) {
   Statement1(s);

   if(condition_2) {
      Statement2(s);
   }
}

<b>Note:-</b>Statement1 would execute if the condition_1 is true. Statement2 would only execute if both the conditions
( condition_1 and condition_2) are true.
`}</pre></figure>
						<br/>	
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h> 
#include<conio.h> 
void main() {
    int num=90;
    if( num < 100 ){
       cout<<"number is less than 100"<<endl;
       if(num > 50){
          cout<<"number is greater than 50";
       } 
    }
    getch();
}
 
Output:
 
number is less than 100
number is greater than 50
 `}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">If else statement</h1>
						<p>The C++ if-else statement also tests the condition. It executes if block if condition is true otherwise else block is executed.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition) {
   Statement(s);
}
else {
   Statement(s);
}`}</pre></figure>
						<br/>	
						<img src={ifelseif} alt="IfElseIf"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h> 
#include<conio.h> 
void main() 						
{ 
    int num=66;
    if( num < 50 ){
       //This would run if above condition is true
       cout<<"num is less than 50";
    }
    else {
       //This would run if above condition is false
       cout<<"num is greater than or equal 50";
    }
   getch();
 }  
 
 Output:
 
 num is greater than or equal 5.
 `}</pre></figure>
<hr className="my-5"/>
<h1 className="mb-2 mt-3">if-else-if ladder Statement</h1>
						<p>if-else-if statement is used when we need to check multiple conditions. In this control structure we have only one “if” and one “else”, however we can have multiple “else if” blocks.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition_1) {
   /*if condition_1 is true execute this*/
   statement(s);
}
else if(condition_2) {
   /* execute this if condition_1 is not met and
    * condition_2 is met
    */
   statement(s);
}
else if(condition_3) {
   /* execute this if condition_1 & condition_2 are
    * not met and condition_3 is met
    */
   statement(s);
}
.
.
.
else {
   /* if none of the condition is true
    * then these statements gets executed
    */
   statement(s);
}`}</pre></figure>
						<br/>	
						<img src={nestedif} alt="NestedIf"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h> 
#include<conio.h> 
void main() 						
{ 
  int num;
  cout<<"Enter an integer number between 1 & 99999: ";
  cin>>num;
  if(num < 100 && num>=1) {
    cout<<"Its a two digit number";
  }
  else if(num < 1000 && num>=100) {
    cout<<"Its a three digit number";
  }
  else if(num < 10000 && num>=1000) {
    cout<<"Its a four digit number";
  }
  else if(num < 100000 && num>=10000) {
    cout<<"Its a five digit number";
  }
  else {
    cout<<"number is not between 1 & 99999";
  }
  getch();
}

Output:

Enter an integer number between 1 & 99999: 8976
Its a four digit number`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
