import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Cvc() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Character Set
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">C Vs C++</h1>

                            <p>Differences between C and C++.</p>
                            <table
                                style={{
                                    border: "2px solid skyblue",
                                    borderCollapse: "collapse",
                                    width: "100%",
                                }}
                            >
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        {["SN","C", "C++"].map((th, i) => (
                                            <th
                                                key={i}
                                                style={{ border: "1px solid skyblue", padding: "5px 10px" }}
                                            >
                                                {th}
                                            </th>
                                        ))}
                                    </tr>
                                </thead>
                                <tbody>
                                    {[
                                        ["1","C follows the procedural style programming.", "C++ is multi-paradigm. It supports both procedural and object oriented."],
                                        ["2","Data is less secured in C.", "In C++, you can use modifiers for class members to make it inaccessible for outside users."],
                                        ["3","C follows the top-down approach.", "C++ follows the bottom-up approach."],
                                        ["4","C does not support function overloading.", "C++ supports function overloading."],
                                        ["5","In C, you can't use functions in structure.", "In C++, you can use functions in structure."],
                                        ["6","C does not support reference variables.", "C++ supports reference variables."],
                                        ["7","In C, scanf() and printf() are mainly used for input/output.", "C++ mainly uses stream cin and cout to perform input and output operations."],
                                        ["8","Operator overloading is not possible in C.", "Operator overloading is possible in C++."],
                                        ["9","C programs are divided into procedures and modules.", "C++ programs are divided into functions and classes."],
                                        ["10","C does not provide the feature of namespace.", "C++ supports the feature of namespace."],
                                        ["11","Exception handling is not easy in C. It has to perform using other functions.", "C++ provides exception handling using Try and Catch block."],
                                        ["12","C does not support the inheritance.", "C++ supports inheritance."],
                                        ["13","C Supports 32 keywords.", "C++ Supports 52 keywords."],
                                    ].map((row, i) => (
                                        <tr key={i}>
                                            {row.map((cell, j) => (
                                                <td
                                                    key={j}
                                                    style={{ border: "1px solid skyblue", padding: "3px 10px" }}
                                                >
                                                    {cell}
                                                </td>
                                            ))}
                                        </tr>
                                    ))}
                                </tbody>
                            </table>

                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
