import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpDatatype() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [sidebarToggled, setSidebarToggled] = useState(false);
  const [sidebarPinned, setSidebarPinned] = useState(false);

  useEffect(() => {
    const storedEmail = sessionStorage.getItem("userEmail");
    if (!storedEmail) {
      navigate("/");
    } else {
      setEmail(storedEmail);
    }
  }, [navigate]);

  const handleLogout = () => {
    sessionStorage.removeItem("userEmail");
    navigate("/");
  };

  const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
  const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

  return (
    <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
      <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

      <main className="page-content">
        {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

        <div className="container-fluid">
          {/* Top Bar */}
          <div className="row d-flex align-items-center p-3 border-bottom">
            <div className="col-md-1">
              <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                <i className="fas fa-bars"></i>
              </button>
            </div>
            <div className="col-md-8">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Home</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    DataType
                  </li>
                </ol>
              </nav>
            </div>
            <div className="col-md-3 text-right">
              <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                {email ? `${email} (Logout)` : "Logout"}
              </button>
            </div>
          </div>

          {/* Main Content */}
          <div className="row p-lg-4">
            <article className="main-content col-md-9 pr-lg-5">

              <h1 className="mb-2 mt-3">Data Type</h1>

              <p>PHP data types are used to hold different types of data or values. PHP supports 8 primitive data types which are followings:</p>
              <ol>
                <li>String</li>
                <li>Integer</li>
                <li>Float (floating point numbers - also called double)</li>
                <li>Boolean</li>
                <li>Array</li>
                <li>Object</li>
                <li>NULL</li>
                <li>Resource</li>
              </ol>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                String
              </h3>
              <p>A string is a non-numeric data type. It holds letters or any alphabets, numbers, and even special characters.</p>
              <p>String values must be enclosed either within single quotes or in double quotes. But both are treated differently.</p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
echo "Hello world!";
echo "<br/>"
echo 'Hello world!';
?>

Output:

Hello world!
Hello world!`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Integer
              </h3>
              <p>
                Integer means numeric data with a negative or positive sign. It holds only whole numbers, i.e., numbers without fractional part or decimal points.
              </p>
              <p>Rules for integer:</p>
              <ul>
                <li>An integer can be either positive or negative.</li>
                <li>An integer must not contain decimal point.</li>
                <li>Integer can be decimal (base 10), octal (base 8), or hexadecimal (base 16).</li>
                <li>The range of an integer must be lie between -2,147,483,648 and 2,147,483,647 i.e., -2^31 to 2^31.</li>

              </ul>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
$x = 5985;
var_dump($x); 
?>

Output:

int(5985)`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Float
              </h3>
              <p>
                A floating-point number is a number with a decimal point. Unlike integer, it can hold numbers with a fractional or decimal point, including a negative or positive sign.
              </p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
$n1 = 19.34;  
$n2 = 54.472;  
$sum = $n1 + $n2;  
echo "Addition of floating numbers: " .$sum;  
?>

Output:

Addition of floating numbers: 73.812`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Boolean
              </h3>
              <p>
                Booleans are the simplest data type works like switch. It holds only two values: TRUE (1) or FALSE (0). It is often used with conditional statements. If the condition is correct, it returns TRUE otherwise FALSE.
              </p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
 if (TRUE)  
 echo "This condition is TRUE.";  
if (FALSE)  
 echo "This condition is FALSE.";
?>

Output:

This condition is TRUE`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Array
              </h3>
              <p>
                An array is a compound data type. It can store multiple values of same data type in a single variable.
              </p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
 $cars = array("Volvo","BMW","Toyota");
 var_dump($cars);
?>

Output:

array(3) {
  [0]=>
  string(5) "Volvo"
  [1]=>
  string(3) "BMW"
  [2]=>
  string(6) "Toyota"
}`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Object
              </h3>
              <p>
                Objects are the instances of user-defined classes that can store both values and functions. They must be explicitly declared.
              </p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
     class bike {  
          function model() {  
               $model_name = "Royal Enfield";  
               echo "Bike Model: " .$model_name;  
             }  
     }  
     $obj = new bike();  
     $obj -> model();
?>

Output:

Bike Model: Royal Enfield`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Null
              </h3>
              <p>
                Null is a special data type that has only one value: NULL. There is a convention of writing it in capital letters as it is case sensitive.<br />
                The special type of data type NULL defined a variable with no value.
              </p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example: sizeof Operators---->`}</div>
                  {`<?php  
    $nl = NULL;  
    echo $nl;   //it will not give any output  
?>

Output:

`}</pre></figure>
              <hr className="my-5" />
              <h3 className="font-weight-bold mb-2">
                Resource
              </h3>
              <p>
                Resources are not the exact data type in PHP. Basically, these are used to store some function calls or references to external PHP resources. For example - a database call. It is an external resource.
              </p>
              <hr className="my-5" />
              <Footer />
            </article>

            <Aside />
          </div>
        </div>
      </main>
    </div>
  )
}
