import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Constant() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [sidebarToggled, setSidebarToggled] = useState(false);
  const [sidebarPinned, setSidebarPinned] = useState(false);

  useEffect(() => {
    const storedEmail = sessionStorage.getItem("userEmail");
    if (!storedEmail) {
      navigate("/");
    } else {
      setEmail(storedEmail);
    }
  }, [navigate]);

  const handleLogout = () => {
    sessionStorage.removeItem("userEmail");
    navigate("/");
  };

  const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
  const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

  return (
    <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
      <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

      <main className="page-content">
        {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

        <div className="container-fluid">
          {/* Top Bar */}
          <div className="row d-flex align-items-center p-3 border-bottom">
            <div className="col-md-1">
              <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                <i className="fas fa-bars"></i>
              </button>
            </div>
            <div className="col-md-8">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Home</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Constant
                  </li>
                </ol>
              </nav>
            </div>
            <div className="col-md-3 text-right">
              <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                {email ? `${email} (Logout)` : "Logout"}
              </button>
            </div>
          </div>

          {/* Main Content */}
          <div className="row p-lg-4">
            <article className="main-content col-md-9 pr-lg-5">

              <h1 className="mb-2 mt-3">Constant</h1>
              <p>PHP constants are name or identifier that can't be changed during the execution of the script except for magic constants, which are not really constants. PHP constants can be defined by 2 ways:</p>
              <ol>
                <li>Using define() function</li>
                <li>Using const keyword</li>
              </ol>

              <hr className="my-5" />
              <h1 className="mb-2 mt-3">constant: define()</h1>
              <p>Use the define() function to create a constant. It defines constant at run time. Let's see the syntax of define() function in PHP.</p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Syntax---->`}</div>
                  {`define(name, value, case-insensitive)  `}</pre></figure>
                  <ol>
							<li>name: It specifies the constant name.</li>
							<li>value: It specifies the constant value.</li>
							<li>case-insensitive: Specifies whether a constant is case-insensitive. Default value is false. It means it is case sensitive by default.</li>
						</ol>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example---->`}</div>
                  {`<?php  
define("MESSAGE","Hello JavaTpoint PHP");  
echo MESSAGE; 
?>

Output:

Hello JavaTpoint PHP`}</pre></figure>
              <hr className="my-5" />
              <h1 className="mb-2 mt-3">constant: const keyword</h1>
						  <p>PHP introduced a keyword const to create a constant. The const keyword defines constants at compile time. It is a language construct, not a function. The constant defined using const keyword are case-sensitive.</p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example---->`}</div>
                  {`<?php  
const MESSAGE="Hello const by JavaTpoint PHP";  
echo MESSAGE;  
?>

Output:

Hello const by JavaTpoint PHP	`}</pre></figure>
              <hr className="my-5" />
              <h1 className="mb-2 mt-3">Constant() function</h1>
						  <p>There is another way to print the value of constants using constant() function instead of using the echo statement.</p>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Syntax---->`}</div>
                  {`constant (name)   `}</pre></figure>
              <figure className="code-block">
                <pre>
                  <div className="text-muted mb-2">{`<!---Example---->`}</div>
                  {`<?php  
define("MSG", "Help");  
echo MSG, "</br>";  
echo constant("MSG");  
//both are similar  
?>

Output:

Help
Help`}</pre></figure>
              <hr className="my-5" />
              <Footer />
            </article>

            <Aside />
          </div>
        </div>
      </main>
    </div>
  )
}
