import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Variable() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Variables
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Variables</h1>

                            <p>A variable is a name of the memory location. It is used to store data. Its value can be changed, and it can be reused many times. It is a way to represent memory location through symbol so that it can be easily identified.</p>
                            <h3 className="mb-2 mt-3">Rules for defining variables</h3>
                            <ul>
                                <li>A variable can have alphabets, digits, and underscore.</li>
                                <li>A variable name can start with the alphabet, and underscore only. It can't start with a digit.</li>
                                <li>No whitespace is allowed within the variable name.</li>
                                <li>A variable name must not be any reserved word or keyword, e.g. int, float, etc.</li>
                            </ul>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`type variable_list;
                                    `}

                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>

{`int a;  
float b;  
char c;`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Types of Variables</h1>
                            <ol>
                                <li>local variable</li>
                                <li>global variable</li>
                                <li>static variable</li>
                                <li>automatic variable</li>
                                <li>external variable</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Local Variable</h1>
                            <p>A variable that is declared and used inside the function or block is called local variable.</p>
                            <p>It’s scope is limited to function or block.It cannot be used outside the block.Local variables need to be initialized before use.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    int x=10;//local variable  
    printf("x = %d", x); 
    getch(); 
} 
Output:

x = 10`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Global Variable</h1>
                            <p>A variable that is declared outside the function or block is called a global variable. It is declared at the starting of program. It is available to all the functions. </p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
int value=20;//global variable  
void function1()
{
  printf("%d\\n" , x);
}
void function2()
{
  printf("%d\\n" , x);
}
void main() {
 
  function1();
  function2();
  getch(); 
} 
Output:

x = 20									
x = 20`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Static Variable</h1>
                            <p>A variable that is declared with the static keyword is called static variable.</p>
                            <p>It retains its value between multiple function calls.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void function1(){  
int x=20;//local variable  
static int y=30;//static variable  
x=x+10;  
y=y+10;  
printf("%d,%d",x,y);  
}  
void main() 						
{ 
    clrscr();
    function1();  
    function1();  
    function1();  
    getch(); 
} 
Output:

30,40
30,50
30,60`}</pre></figure>
                            <br />
                            <p>If you call this function many times, the local variable will print the same value for each function call, e.g, 11,11,11 and so on. But the static variable will print the incremented value in each function call, e.g. 11, 12, 13 and so on.</p>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Automatic Variable</h1>
                            <p>All variables in C that are declared inside the block, are automatic variables by default. We can explicitly declare an automatic variable using auto keyword.Automatic variables are similar as local variables.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void function()
{
  int x=10;//local variable (also automatic)  
  auto int y=20;//automatic variable
}
void main() {
    function();
    getch();
}`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">External Variable</h1>
                            <p>External variable can be shared between multiple C files.We can declare external variable using extern keyword.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`//myfile.h

extern int x=10;//external variable (also global)  

//program1.c
  #include "myfile.h"  
  #include<stdio.h>  
  void printValue(){  
  printf("Global variable: %d", global_variable);  
  }`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
