import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function MagicConst() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [sidebarToggled, setSidebarToggled] = useState(false);
  const [sidebarPinned, setSidebarPinned] = useState(false);

  useEffect(() => {
    const storedEmail = sessionStorage.getItem("userEmail");
    if (!storedEmail) {
      navigate("/");
    } else {
      setEmail(storedEmail);
    }
  }, [navigate]);

  const handleLogout = () => {
    sessionStorage.removeItem("userEmail");
    navigate("/");
  };

  const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
  const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

  return (
    <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
      <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

      <main className="page-content">
        {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

        <div className="container-fluid">
          {/* Top Bar */}
          <div className="row d-flex align-items-center p-3 border-bottom">
            <div className="col-md-1">
              <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                <i className="fas fa-bars"></i>
              </button>
            </div>
            <div className="col-md-8">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Home</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Magic Constant
                  </li>
                </ol>
              </nav>
            </div>
            <div className="col-md-3 text-right">
              <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                {email ? `${email} (Logout)` : "Logout"}
              </button>
            </div>
          </div>

          {/* Main Content */}
          <div className="row p-lg-4">
            <article className="main-content col-md-9 pr-lg-5">

              <h1 className="mb-2 mt-3">Magic Constants</h1>
              <p>Magic constants are the predefined constants in PHP which get changed on the basis of their use. They start with double underscore (__) and ends with double underscore. They are similar to other predefined constants but as they change their values with the context, they are called magic constants. There are nine magic constants in PHP. In which eight magic constants start and end with double underscores (__).</p>
              <ol>
                <li>__LINE__</li>
                <li>__FILE__</li>
                <li>__DIR__</li>
                <li>__FUNCTION__</li>
                <li>__CLASS__</li>
                <li>__TRAIT__</li>
                <li>__METHOD__</li>
                <li>__NAMESPACE__</li>
                <li>ClassName::class</li>
              </ol>

              <hr className="my-5"/>
                <h1 className="mb-2 mt-3">__LINE__</h1>
                <p>It returns the current line number of the file, where this constant is used.</p>
                <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __LINE__</h3>";    
// print Your current line number i.e;4     
echo "You are at line number " . __LINE__ . "<br><br>"; ?> 

Output:

Example for __LINE__

You are at line number 4`}</pre></figure>
                <hr className="my-5" />
                <h1 className="mb-2 mt-3">__FILE__</h1>
						<p>This magic constant returns the full path of the executed file, where the file is stored. If it is used inside the include, the name of the included file is returned.</p>
                <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __FILE__</h3>";    
//print full path of file with .php extension    
echo __FILE__ . "<br><br>";  
?>

Output:

Example for __FILE__

D:\\xampp\\htdocs\\program\\magic.php`}</pre></figure>
                <hr className="my-5" />
                <h1 className="mb-2 mt-3">__DIR__</h1>
						    <p>It returns the full directory path of the executed file. The path returned by this magic constant is equivalent to dirname(__FILE__). This magic constant does not have a trailing slash unless it is a root directory.</p>
                <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __DIR__</h3>";    
//print full path of directory where script will be placed    
echo __DIR__ . "<br><br>";  
//below output will equivalent to above one.  
echo dirname(__FILE__) . "<br><br>";    

?>

Output:

Example for __DIR__

D:\\xampp\\htdocs\\program

D:\\xampp\\htdocs\\program`}</pre></figure>
<hr className="my-5" />
                <h1 className="mb-2 mt-3">__FUNCTION__</h1>
						  <p>This magic constant returns the function name, where this constant is used. It will return blank if it is used outside of any function.</p>
                <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __FUNCTION__</h3>";    
//Using magic constant inside function.    
function test(){    
    //print the function name i.e; test.   
    echo 'The function name is '. __FUNCTION__ . "<br><br>";   
}    
test();    
  
//Magic constant used outside function gives the blank output.    
function test_function(){    
    echo 'Hie';    
}    
test_function();    
//give the blank output.   
echo  __FUNCTION__ . "<br><br>";  

?>

Output:

Example for __FUNCTION__

The function name is test

Hie`}</pre></figure>
<hr className="my-5"/>
                        <h1 className="mb-2 mt-3">__CLASS__</h1>
						<p>It returns the class name, where this magic constant is used. __CLASS__ constant also works in traits.</p>
            <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __CLASS__</h3>";    
class JTP    
{    
    public function __construct() {    
        ;    
}    
function getClassName(){    
    //print name of the class JTP.   
    echo __CLASS__ . "<br><br>";   
    }    
}    
$t = new JTP;    
$t->getClassName();    
  
//in case of multiple classes   
class base  
{    
function test_first(){    
        //will always print parent class which is base here.    
        echo __CLASS__;   
    }    
}    
class child extends base    
{    
    public function __construct() {    
        ;    
    }    
}    
$t = new child;    
$t->test_first();    
?>

Output:

Example for __CLASS__

JTP

base`}</pre></figure>
<hr className="my-5"/>
                        <h1 className="mb-2 mt-3">__TRAIT__</h1>
						<p>This magic constant returns the trait name, where it is used.</p>
            <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __TRAIT__</h3>";    
trait created_trait {    
    function jtp(){    
        //will print name of the trait i.e; created_trait    
        echo __TRAIT__;  
    }    
}    
class Company {    
    use created_trait;    
    }    
$a = new Company;    
$a->jtp();    

?&gt;

Output:

Example for __TRAIT__

created_trait`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">__METHOD__</h1>
						<p>It returns the name of the class method where this magic constant is included. The method name is returned the same as it was declared.</p>
            <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __METHOD__</h3>";  
class method {    
    public function __construct() {    
        //print method::__construct    
            echo __METHOD__ . "<br><br>";   
        }    
    public function meth_fun(){    
        //print method::meth_fun    
            echo __METHOD__;   
    }    
}    
$a = new method;    
$a->meth_fun();  


?>

Output:

Example for __METHOD__

method:: construct
method:: meth_fun`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">__NAMESPACE__</h1>
						<p>It returns the current namespace where it is used.</p>
            <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
echo "<h3>Example for __NAMESPACE__</h3>";  
class name {    
    public function __construct() {    
        echo 'This line will print on calling namespace.';     
    }     
}    
$class_name = __NAMESPACE__ . '\\name';    
$a = new class_name;   


?>

Output:

Example for __NAMESPACE__

This line will print on calling namespace.`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">ClassName::class</h1>
						<p>This magic constant does not start and end with the double underscore (__). It returns the fully qualified name of the ClassName. ClassName::class is added in PHP 5.5.0. It is useful with namespaced classes.</p>
            <figure className="code-block">
                  <pre>
                    <div className="text-muted mb-2">{`<!---Example---->`}</div>
                    {`<?php  
namespace Technical_Portal;  
echo "<h3>Example for CLASSNAME::CLASS </h3>";  
class javatpoint {    
}  
echo javatpoint::class;    //ClassName::class   
?&gt;

Output:

Example for ClassName::class

Technical_Portal\\javatpoint`}</pre></figure>

                <hr className="my-5" />
                <Footer />
            </article>

            <Aside />
          </div>
        </div>
      </main>
    </div>
  )
}
