import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Allocation() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Dynamic Memory Allocation
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                        <h1 className="mb-2 mt-3">Dynamic memory allocation</h1>
                        
                        <p>The concept of dynamic memory allocation in c language enables the C programmer to allocate memory at runtime. Dynamic memory allocation in c language is possible by 4 functions of stdlib.h header file.</p>
						<ul>
							<li>malloc()</li>
							<li>calloc()</li>
							<li>realloc()</li>
							<li>free()</li>
						</ul>
                        <hr className="my-5"/>
                        <p>Before learning above functions, let's understand the difference between static memory allocation and dynamic memory allocation.</p>
						<table
  style={{
    width: "100%",
    border: "2px solid skyblue",
    borderCollapse: "collapse",
  }}
>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>
        static memory allocation
      </th>
      <th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>
        dynamic memory allocation
      </th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        memory is allocated at compile time.
      </td>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        memory is allocated at run time.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        memory can't be increased while executing program.
      </td>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        memory can be increased while executing program.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        used in array.
      </td>
      <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
        used in linked list.
      </td>
    </tr>
  </tbody>
</table>

						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">malloc() function</h1>
						<p>The malloc() function allocates single block of requested memory. It doesn't initialize memory at execution time, so it has garbage value initially. It returns NULL if memory is not sufficient. The syntax of malloc() function is given below:</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`ptr=(cast-type*)malloc(byte-size) `}</pre></figure>

<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<stdlib.h> 
void main() 						
{ 
    int n,i,*ptr,sum=0;    
    printf("Enter number of elements: ");    
    scanf("%d",&n);    
    ptr=(int*)malloc(n*sizeof(int));  //memory allocated using malloc    
    if(ptr==NULL)                         
    {    
        printf("Sorry! unable to allocate memory");    
        exit(0);    
    }    
    printf("Enter elements of array: ");    
    for(i=0;i<n;++i)    
    {    
        scanf("%d",ptr+i);    
        sum+=*(ptr+i);    
    }    
    printf("Sum=%d",sum);    
    free(ptr);     
    getch(); 
} 

Output:

Enter elements of array: 3
Enter elements of array: 10
10
10
Sum=30`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">calloc() function</h1>
                        <p>The calloc() function allocates multiple block of requested memory. It initially initialize all bytes to zero. It returns NULL if memory is not sufficient. The syntax of calloc() function is given below:</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`ptr=(cast-type*)calloc(number, byte-size)  `}</pre></figure>
						
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h>  
#include<stdlib.h>
void main() {
    int n,i,*ptr,sum=0;    
    printf("Enter number of elements: ");    
    scanf("%d",&n);    
    ptr=(int*)calloc(n,sizeof(int));  //memory allocated using calloc    
    if(ptr==NULL)                         
    {    
        printf("Sorry! unable to allocate memory");    
        exit(0);    
    }    
    printf("Enter elements of array: ");    
    for(i=0;i<n;++i)    
    {    
        scanf("%d",ptr+i);    
        sum+=*(ptr+i);    
    }    
    printf("Sum=%d",sum);    
    free(ptr);    
    getch(); 
}
 
Output:

Enter elements of array: 3
Enter elements of array: 10
10
10
Sum=30`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">realloc() function</h1>
                        <p>If memory is not sufficient for malloc() or calloc(), you can reallocate the memory by realloc() function. In short, it changes the memory size.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`ptr=realloc(ptr, new-size)`}</pre></figure>
                        
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">free()</h1>
                        <p>The memory occupied by malloc() or calloc() functions must be released by calling free() function. Otherwise, it will consume memory until program exit.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`free(ptr) `}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
