import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Pstructure() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Program Structure
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Program Structure</h1>

                            <p>Structure of C program includes the following : Documentation, Preprocessor, Constant Declaration, Global Declaration, main(), Subprograms.</p>

                            <figure className="code-block">
  <pre>
    {`Documentation
----------------------------------

Preprocessor
----------------------------------

Constant Declaration
----------------------------------

Global Declaration
----------------------------------

main()
{
    // body of main()
}
----------------------------------

Subprograms
Function1()
Function2()
|
|
FunctionN()
`}
  </pre>
</figure>

                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Note:
                            </h3>
                            <ul>
                                <li>Documentation may includes the information about the program, author, date of creation of program, etc. as comments.</li>
                                <li>Preprocessor tells the compiler to execute function, defines in header file, before the execution of main().</li>
                                <li>In Constant Declaration section, we declare constant variables, we can't change the value of constant variables throughout the program and we must initialize constant variables with declaration.</li>
                                <li>In Global Declaration section, we declare global variables, global variables are those variables that can be accessed by all function in a program.</li>
                                <li>main() is the first function to get executed. The main() serves as the starting point for program execution. It usually controls program execution by directing the calls to other functions in the program. A program usually stops executing at the end of main.The execution of main() begins with opening brace and ends with closing brace.</li>
                                <li>The subprogram section consists all the definition of user-defined functions that are called in the main().</li>
                            </ul>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
