import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpMath() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [sidebarToggled, setSidebarToggled] = useState(false);
  const [sidebarPinned, setSidebarPinned] = useState(false);

  useEffect(() => {
    const storedEmail = sessionStorage.getItem("userEmail");
    if (!storedEmail) {
      navigate("/");
    } else {
      setEmail(storedEmail);
    }
  }, [navigate]);

  const handleLogout = () => {
    sessionStorage.removeItem("userEmail");
    navigate("/");
  };

  const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
  const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

  return (
    <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
      <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

      <main className="page-content">
        {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

        <div className="container-fluid">
          {/* Top Bar */}
          <div className="row d-flex align-items-center p-3 border-bottom">
            <div className="col-md-1">
              <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                <i className="fas fa-bars"></i>
              </button>
            </div>
            <div className="col-md-8">
              <nav aria-label="breadcrumb">
                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                  <li className="breadcrumb-item">
                    <Link to="/dashboard">Home</Link>
                  </li>
                  <li className="breadcrumb-item active" aria-current="page">
                    Math Function
                  </li>
                </ol>
              </nav>
            </div>
            <div className="col-md-3 text-right">
              <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                {email ? `${email} (Logout)` : "Logout"}
              </button>
            </div>
          </div>
          {/* Main Content */}
          <div className="row p-lg-4">
            <article className="main-content col-md-9 pr-lg-5">

              <h1 className="mb-2 mt-3">math Function</h1>
              <p>PHP provides many predefined math constants and functions that can be used to perform mathematical operations.</p>
              <br />
              <h3 className="mb-2 mt-3">pi() Function</h3>
              <p>The pi() function returns the value of PI:</p>

              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
	echo(pi()); 
?>

Output:

3.1415926535898
`}</pre></figure>
              <hr className="my-5" />
              <h1 className="mb-2 mt-3">min() and max() Functions</h1>
              <p>The min() and max() functions can be used to find the lowest or highest value in a list of arguments:</p>
              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
echo(min(0, 150, 30, 20, -8, -200) . "<br>");
echo(max(0, 150, 30, 20, -8, -200));
?>

Output:

-200
150`}</pre></figure>
              <hr className="my-5" />
              <h1 className="mb-2 mt-3">abs() Function</h1>
              <p>The abs() function returns the absolute (positive) value of a number:</p>
              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
	echo(abs(-6.7));
?>

Output:

6.7`}</pre></figure>
              <hr className="my-5" />
              <h3 className="mb-2 mt-3">sqrt() function</h3>
              <p>The sqrt() function returns the square root of a number:</p>
              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
echo(sqrt(64) . "<br>");
echo(sqrt(0) . "<br>");
echo(sqrt(1) . "<br>");
echo(sqrt(9));
?>

Output:

8
0
1
3`}</pre></figure>

              <hr className="my-5" />
              <h1 className="mb-2 mt-3">round() function</h1>
              <p>The round() function rounds a floating-point number to its nearest integer:</p>
              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
echo(round(0.60) . "<br>");
echo(round(0.50) . "<br>");
echo(round(0.49) . "<br>");
echo(round(-4.40) . "<br>");
echo(round(-4.60));
?>

Output:

1
1
0
-4
-5`}</pre></figure>
              <hr className="my-5" />
              <h1 className="mb-2 mt-3">Random Numbers</h1>
              <p>The rand() function generates a random number:</p>
              <figure className="code-block">
                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                  {`<?php 
echo(rand());
echo(rand(10, 100));
?>

Output:

1381167960
97`}</pre></figure>
              <hr className="my-5" />
              <Footer />
            </article>

            <Aside />
          </div>
        </div>
      </main>
    </div>
  )
}

