import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Functions() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Functions
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                        <h1 className="mb-2 mt-3">Function</h1>
                        
                        <p>In c, we can divide a large program into the basic building blocks known as function. The function contains the set of programming statements enclosed by {}. A function can be called multiple times to provide reusability and modularity to the C program. In other words, we can say that the collection of functions creates a program. The function is also known as procedureor subroutinein other programming languages.</p>
						<p>There are the following advantages of C functions:</p>
						<ul>
							<li>By using functions, we can avoid rewriting same logic/code again and again in a program.</li>
							<li>We can call C functions any number of times in a program and from any place in a program.</li>
							<li>We can track a large C program easily when it is divided into multiple functions.</li>
							<li>Reusability is the main achievement of C functions.</li>
							<li>However, Function calling is always a overhead in a C program.</li>
						</ul>
                        <hr className="my-5"/>
						<h1 className="mb-2 mt-3">Function Aspects</h1>
                        <ol>
							<li><b>Function declaration:</b> A function must be declared globally in a c program to tell the compiler about the function name, function parameters, and return type.</li>
							<li><b>Function call</b> Function can be called from anywhere in the program. The parameter list must not differ in function calling and function declaration. We must pass the same number of functions as it is declared in the function declaration.</li>
							<li><b>Function definition</b> It contains the actual statements which are to be executed. It is the most important aspect to which the control comes when the function is called. Here, we must notice that only one value can be returned from the function.</li>
						</ol>
						<table
  style={{
    border: "3px solid skyblue",
    borderCollapse: "collapse",
    width: "100%",
  }}
>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ border: "1px solid skyblue", padding: "6px 10px" }}>SN</th>
      <th style={{ border: "1px solid skyblue", padding: "6px 10px" }}>Function Aspects</th>
      <th style={{ border: "1px solid skyblue", padding: "6px 10px" }}>Syntax</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>1</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Function Declaration</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>
        <code>return_type function_name (argument_list);</code>
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>2</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Function Call</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>
        <code>function_name (argument_list);</code>
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>3</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>Function Definition</td>
      <td style={{ border: "1px solid skyblue", padding: "5px 10px" }}>
        <code>
          return_type function_name (argument_list) {'{'} function body; {'}'}
        </code>
      </td>
    </tr>
  </tbody>
</table>

                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
int max(int x, int y) 
{ 
    if (x > y) 
      return x; 
    else
      return y; 
} 

void main(void) 
{ 
    int a = 10, b = 20; 
    // Calling above function to find max of 'a' and 'b' 
    int m = max(a, b); 
    printf("m is %d", m); 
    getch(); 
} 

Output:

m is 20`}</pre></figure>
<p>Nested Function</p>
<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
main(void) {
   auto int my_fun();
   my_fun();
   printf("Main Function\\n");
   int my_fun() {
      printf("my_fun function\\n");
   }
   printf("Done");
}

Output:

my_fun function
Main Function
Done`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Types of Functions</h1>
                        <p>There are two types of functions in C programming:</p>
						<ol>
							<li><b>Library Functions:</b> are the functions which are declared in the C header files such as scanf(), printf(), gets(), puts(), ceil(), floor() etc.</li>
							<li><b>User-defined functions:</b> are the functions which are created by the C programmer, so that he/she can use it many times. It reduces the complexity of a big program and optimizes the code.</li> 
						</ol>
						<hr className="my-5"/>
						<p>There are two ways that a C function can be called from a program. They are,</p>
						<ol>	
							<li>Call by value</li>
							<li>Call by reference</li>
						</ol>
						<h3 className="mb-2 mt-3">Call By Value</h3>
						<ul>
							<li>In call by value method, the value of the variable is passed to the function as parameter.</li>
							<li>The value of the actual parameter can not be modified by formal parameter.</li>
							<li>Different Memory is allocated for both actual and formal parameters. Because, value of actual parameter is copied to formal parameter.</li>
						</ul>
						
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void swap(int a, int b);          
void main()
{
    int m = 22, n = 44;
    // calling swap function by value
    printf(" values before swap  m = %d \\nand n = %d", m, n);
    swap(m, n);                         
    getch();
}
void swap(int a, int b)
{ 
    int tmp;
    tmp = a;
    a = b;
    b = tmp;
    printf(" \\nvalues after swap m = %d\\n and n = %d", a, b);
}
 
Output:

values before swap m = 22
and n = 44
values after swap m = 44
and n = 22`}</pre></figure>
						<hr className="my-5"/>
						<h3 className="mb-2 mt-3">CALL BY REFERENCE:</h3>
                        <ul>
							<li>In call by reference method, the address of the variable is passed to the function as parameter.</li>
							<li>The value of the actual parameter can be modified by formal parameter.</li>
							<li>Same memory is used for both actual and formal parameters since only address is used by both parameters.</li>
						</ul>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void swap(int *a, int *b); 
void main()
{
    int m = 22, n = 44;
    //  calling swap function by reference
    printf("values before swap m = %d \n and n = %d",m,n);
    swap(&m, &n);         	
}
void swap(int *a, int *b)
{
    int tmp;
    tmp = *a;
    *a = *b;
    *b = tmp;
    printf("\\n values after swap a = %d \\nand b = %d", *a, *b);
}

Output:

values before swap m = 22 
and n = 44
values after swap a = 44
and b = 22`}</pre></figure>
<hr className="my-5"/>
						<h3 className="mb-2 mt-3">Function Prototype:</h3>
                        <p>A function prototype is simply the declaration of a function that specifies function's name, parameters and return type. It doesn't contain function body. A function prototype gives information to the compiler that the function may later be used in the program.</p>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void function(int); //prototype
main() {
   function(50);
}
void function(int x) {
   printf("The value of x is: %d", x);
}

Output:

The value of x is: 50`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
