import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Mail() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Mail() Function
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Mail() Function</h1>
                            <p>Sending email messages are very common for a web application, for example, sending welcome email when a user create an account on your website, sending newsletters to your registered users, or getting user feedback or comment through website's contact form, and so on. You can use the PHP built-in mail() function for creating and sending email messages to one or more recipients dynamically from your PHP application either in a plain-text form or formatted HTML. The basic syntax of this function can be given with:</p>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`mail(to, subject, message, headers, parameters)
                                    `}
                                </pre></figure>
                            <table style={{ borderCollapse: "collapse", width: "100%", border: "2px solid skyblue" }}>
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>Parameter</th>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>Description</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>to</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>The recipient's email address.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>subject</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Subject of the email to be sent. This parameter i.e. the subject line cannot contain any newline character (\n).</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>message</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Defines the message to be sent. Each line should be separated with a line feed-LF (\n). Lines should not exceed 70 characters.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>headers</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>This is typically used to add extra headers such as "From", "Cc", "Bcc". The additional headers should be separated with a carriage return plus a line feed-CRLF (\r\n).</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>parameters</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Used to pass additional parameters.</td>
                                    </tr>
                                </tbody>
                            </table>


                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Sending Plain Text Emails</h1>
                            <p>The simplest way to send an email with PHP is to send a text email. In the example below we first declare the variables — recipient's email address, subject line and message body — then we pass these variables to the mail() function to send the email.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$to = 'maryjane@email.com';
$subject = 'Marriage Proposal';
$message = 'Hi Jane, will you marry me?'; 
$from = 'peterparker@email.com';
 
// Sending email
if(mail($to, $subject, $message)){
    echo 'Your mail has been sent successfully.';
} else{
    echo 'Unable to send email. Please try again.';
}
?>
`}
                                </pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Sending HTML Formatted Emails</h1>
                            <p>When you send a text message using PHP, all the content will be treated as simple text. We're going to improve that output, and make the email into a HTML-formatted email. To send an HTML email, the process will be the same. However, this time we need to provide additional headers as well as an HTML formatted message.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$to = 'maryjane@email.com';
$subject = 'Marriage Proposal';
$from = 'peterparker@email.com';
 
// To send HTML mail, the Content-type header must be set
$headers  = 'MIME-Version: 1.0' . "\\r\\n";
$headers .= 'Content-type: text/html; charset=iso-8859-1' . "\\r\\n";
 
// Create email headers
$headers .= 'From: '.$from."\\r\\n".
    'Reply-To: '.$from."\\r\\n" .
    'X-Mailer: PHP/' . phpversion();
 
// Compose a simple HTML email message
$message = '<html><body>';
$message .= '<h1 style="color:#f40;">Hi Jane!</h1>';
$message .= '<p style="color:#080;font-size:18px;">Will you marry me?</p>';
$message .= '</body></html>';
 
// Sending email
if(mail($to, $subject, $message, $headers)){
    echo 'Your mail has been sent successfully.';
} else{
    echo 'Unable to send email. Please try again.';
}
 
?> `}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
