import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import Single from '../../../Images/data/c++/single_inheritance.png';
import multiple from '../../../Images/data/c++/multiple_inheritance.png';
import multilevel from '../../../Images/data/c++/multilevel_inheritance.png';
import hierarchical from '../../../Images/data/c++/hierarchical_inheritance.png';
import hybrid from '../../../Images/data/c++/Hybrid_Inheritance.png';

export default function Inheritance() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Inheritance
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                        <h1 className="mb-2 mt-3">Inheritance</h1>
						<p>Inheritance are 5 types</p>
						<ul>
							<li>Single Inheritance</li>
							<li>Multiple Inheritance</li>
							<li>Multilevel Inheritance</li>
							<li>Hybrid Inheritance</li>
							<li>Hierarchical Inheritance</li>
						</ul>
                        <hr className="my-5"/>
                        <h3 className="font-weight-bold mb-2">
                            Single Inheritance
                        </h3>
						<p>In this, a class is allowed to inherit from only one class. i.e. one sub class is inherited by one base class only.</p>
                            <center><img src={Single} alt="Single"/></center>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`class subclass_name : access_mode base_class
{
 //body of subclass
};`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class A{
  public:
  void show()
  {
    cout << "This is a A Class" << endl;
  }
};
class B: public A{
};
void main()
{
  B obj;
  obj.show();
  getch();
}
   
Output:
   
This is a A Class 
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                            Multiple Inheritance
                        </h3>
						<p>Multiple Inheritance is a feature of C++ where a class can inherit from more than one classes. i.e one sub class is inherited from more than one base classes</p>
						<img src={multiple} alt="Multiple"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`class Base_class_Name1
{
 // body_of_Base_class
};

class Base_class_Name2
{
 //body_of_Base_class2
};

class Derived_class_Name : Access_Specifier Base_class_Name1, Access_Specifier
Base_cass_Name2,.......
{
 //body_of_Derived_class2
};
`}</pre></figure>
<br/>
						Here, the number of base classes will be separated by a comma (‘, ‘) and access mode for every base class must be specified. <br/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class A{
  public:
  void show()
  {
    cout << "This is a A Class" << endl;
  }
};
class B {
  public:
  void display()
  {
    cout << "This is a B Class" << endl;
  }
};
class C: public A, public B {
};
void main()
{
  C obj;
  obj.show();
  obj.display();
  getch();
} 
   
Output:
   
This is a A Class
This is a B Class
   
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Multilevel Inheritance
                            </h3>
                            <p>In this type of inheritance, a derived class is created from another derived class.</p>
						<img src={multilevel} alt="Multilevel"/>
                        
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Class Syntax -->`}</div>
                                    {`class Base_class_Name
{
 // body_of_Base_class
};

class Derived_class_Name1 : Access_Specifier Base_class_Name
{
 //body_of_Derived_class1
};

class Derived_class_Name2 : Access_Specifier Derived_class_Name1
{
 //body_of_Derived_class2
};`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class A
{
 public:
 A()
 {
 cout << "Hello" << endl;
 }
};
class B: public A
{ 
 public:
 B()
 {
 cout<<"Everyone" << endl;
 }
};
class C: public B{
 public:
 C()
 {
 cout<<"Good Morning" << endl;
 }
};
void main()
{
 C obj;
 getch();
} 

Output:

Hello
Everyone
Good Morning

`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                            Hierarchical Inheritance
                        </h3>
						<p> In this type of inheritance, more than one sub class is inherited from a single base class. i.e. more than one derived class is created from a single base class.</p>
						<img src={hierarchical} alt="Hierarchical"/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Class Syntax -->`}</div>
                                    {`class Base_classname
{
//body_of_Base_class
};

class derived_class_Name1 : Access_Specifier Base_class_Name
{
//body_of_derived_class1
};

class derived_class_Name2 : Access_Specifier Base_class_Name
{
//body_of_derived_class2
};`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Vehicle
{
 public:
 Vehicle() 
 {
 cout << "This is a Vehicle" << endl;
 }
};
class Car: public Vehicle
{
};
class Bus: public Vehicle
{
};
void main()
{
 Car obj1;
 Bus obj2;
 getch();
} 

Output:

This is a Vehicle
This is a Vehicle

`}</pre></figure>
<hr className="my-5"/>
                        <h3 className="font-weight-bold mb-2">
                            Hybrid (Virtual) Inheritance
                        </h3>
						<p> Hybrid Inheritance is implemented by combining more than one type of inheritance. For example: Combining Hierarchical inheritance and Multiple Inheritance.</p>
						<img src={hybrid} alt="Hybrid"/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Class Syntax -->`}</div>
                                    {`class base_class_Name
{
//body_of_Base_class
};

class derived_class_Name1 : Access_Specifier base_class_Name
{
//body_of_derived_class1
};

class derived_class_Name2
{
//body_of_derived_class2
};

class derived_class_Name3 : class derived_class_Name1, class derived_class_Name2
{
//body_of_derived_class3
};
`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Vehicle
{
 public: 
 Vehicle()
 {
  cout << "This is a Vehicle" << endl;
 }
};
class Fare
{
 public:
 Fare()
 {
 cout<<"Fare of Vehicle\n";
 }
};
class Car: public Vehicle
{
};
class Bus: public Vehicle, public Fare
{
};
void main()
{
 Bus obj2;
 getch();
} 

Output:

This is a Vehicle
Fare of Vehicle

`}</pre></figure>
                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}