import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import ife from '../../../Images/data/if.jpeg';
import ifelse from '../../../Images/data/ifelse.jpeg';
import ifelseif from '../../../Images/data/ifelseif.png';
import nestedif from '../../../Images/data/nestedif.png';

export default function Ifelse() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Decision Making
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                    <article className="main-content col-md-9 pr-lg-5">
                        
                        <h1 className="mb-2 mt-3">Decision Making</h1>
                        
                        <p>Decision making structures require that the programmer specifies one or more conditions to be evaluated or tested by the program, along with a statement or statements to be executed if the condition is determined to be true, and optionally, other statements to be executed if the condition is determined to be false.</p>
						<p>There are the following variants of if statement in C language.</p>
						<ul>
							<li>If statement</li>
							<li>If-else statement</li>
							<li>If else-if ladder</li>
							<li>Nested  if</li>
						</ul>
                        <hr className="my-5"/>
						<h1 className="mb-2 mt-3">If Statement</h1>
                        <p>The if statement is used to check some given condition and perform some operations depending upon the correctness of that condition. It is mostly used in the scenario where we need to perform the different operations for the different conditions. The syntax of the if statement is given below.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(expression){  
//code to be executed  
}		`}</pre></figure>
<br/>	
						<img src={ife} alt="If"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h> 
#include<conio.h> 
void main() 						
{ 
  int number=0;    
  printf("Enter a number:");    
  scanf("%d",&number);    
  if(number%2==0){    
    printf("%d is even number",number);    
  }    
  getch(); 
} 
Output:

Enter a number:4
4 is even number
enter a number:5`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">If-else Statement</h1>
                        <p>The if statement alone tells us that if a condition is true it will execute a block of statements and if the condition is false it won’t. But what if we want to do something else if the condition is false. Here comes the C else statement. We can use the else statement with if statement to execute a block of code when the condition is false.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(expression){  
  //code to be executed if condition is true  
}else{  
  //code to be executed if condition is false  
}`}</pre></figure>
						<br/>	
						<img src={ifelse} alt="IfElse"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h> 
#include<conio.h> 
void main() {
  int age;   
  printf("Enter your age:");   
  scanf("%d",&age);  
  if(age>=18)  
  {  
    printf("You are eligible to vote...");   
  }  
  else   
  {  
    printf("Sorry ... you can't vote");   
  }  
  getch(); 
}
 
Output:

Enter your age:18
You are eligible to vote...

Enter your age:13
Sorry ... you can't vote`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">If else-if ladder Statement</h1>
                        <p>The if-else-if ladder statement is an extension to the if-else statement. It is used in the scenario where there are multiple cases to be performed for different conditions. In if-else-if ladder statement, if a condition is true then the statements defined in the if block will be executed, otherwise if some other condition is true then the statements defined in the else-if block will be executed, at the last if none of the condition is true then the statements defined in the else block will be executed. There are multiple else-if blocks possible. It is similar to the switch case statement where the default is executed instead of else block if none of the cases is matched.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition1){  
  //code to be executed if condition1 is true  
}else if(condition2){  
  //code to be executed if condition2 is true  
}  
else if(condition3){  
  //code to be executed if condition3 is true  
}  
...  
else{  
  //code to be executed if all the conditions are false  
}`}</pre></figure>
						<br/>	
						<img src={ifelseif} alt="IfElseIf"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h> 
#include<conio.h> 
void main() 						
{ 
  clrscr();
  int number=0;    
  printf("enter a number:");    
  scanf("%d",&number);     
  if(number==10){    
    printf("number is equals to 10");    
  }    
  else if(number==50){    
    printf("number is equal to 50");    
  }    
  else if(number==100){    
    printf("number is equal to 100");    
  }    
  else{    
    printf("number is not equal to 10, 50 or 100");    
  }    
  getch(); 
} 
Output:

enter a number:4
number is not equal to 10, 50 or 100
enter a number:50
number is equal to 50`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">nested-if Statement</h1>
                        <p>A nested if is an if statement that is the target of another if statement. Nested if statements means an if statement inside another if statement.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if (condition1) 
{
   // Executes when condition1 is true
   if (condition2) 
   {
      // Executes when condition2 is true
   }
}`}</pre></figure>
						<br/>	
						<img src={nestedif} alt="NestedIf"/>
						<br/>	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h> 
#include<conio.h> 
void main() 						
{ 
    clrscr();
    int i = 10; 
    if (i == 10) 
    { 
        // First if statement 
        if (i < 15) 
           printf("i is smaller than 15\\n"); 
  
        // Nested - if statement 
        // Will only be executed if statement above 
        // is true 
        if (i < 12) 
            printf("i is smaller than 12 too\\n"); 
        else
            printf("i is greater than 15"); 
    }     
    getch(); 
} 
Output:

i is smaller than 15
i is smaller than 12 too`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
