import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Cincout() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Basic Input/Output
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">
                        <h1 className="mb-2 mt-3">Basic Input/Output</h1>
                        
                        <p>C++ I/O operation is using the stream concept. Stream is the sequence of bytes or flow of data. It makes the performance fast. If bytes flow from main memory to device like printer, display screen, or a network connection, etc, this is called as output operation. If bytes flow from device like printer, display screen, or a network connection, etc to main memory, this is called as input operation.</p>
						<hr className="my-5"/>
                        <h1 className="mb-2 mt-3">Standard output stream (cout)</h1>

                            <p>The cout is a predefined object of ostream class. It is connected with the standard output device, which is usually a display screen. The cout is used in conjunction with stream insertion operator (&lt;&lt;) to display the output on a console</p>
                           
                            <figure className="code-block">
                                <pre>
                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main()
{
  char ary[] = "Welcome";  
  cout << "Value of ary is: " << ary << endl;  
  getch();
}
    
Output:
    
Value of ary is: Welcome`}
                                </pre>
                            </figure>

                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Standard input stream (cin)</h1>

                            <p>The cin is a predefined object of istream class. It is connected with the standard input device, which is usually a keyboard. The cin is used in conjunction with stream extraction operator (&gt;&gt;) to read the input from a console.</p>
                            <figure className="code-block">
                                <pre>
                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main()
{
  int age;  
  cout << "Enter your age: ";  
  cin >> age;  
  cout << "Your age is: " << age << endl;  
  getch();
}
    
Output:
    
Enter your age: 22
Your age is: 22`}
                                </pre>
                            </figure>    			<hr className="my-5" />
                            <h1 className="mb-2 mt-3">Standard end line (endl)</h1>

                            <p>The endl is a predefined object of ostream class. It is used to insert a new line characters and flushes the stream.</p>
                            <figure className="code-block">
                                <pre>
                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main()
{
  cout << "Tutorial";     
  cout << " Samrat" << endl;   
  cout << "End of line" << endl; 
  getch();
}
    
Output:
    
Tutorial Samrat
End of line`}
                                </pre>
                            </figure>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
