import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Access() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Access Modifier
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Access Modifier</h1>

                            <ul>
                                <li>The commands that are used to specify the access level of class members.</li>
                                <li>These are used to enforce restrictions to members of a class.</li>
                            </ul>

                            <p className="lead mb-3">There are three access specifiers or modifiers:- </p>
                            <ol>
                                <li>public</li>
                                <li>protected</li>
                                <li>private</li>
                            </ol>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`Class base{
  public:
  //public members go here
  protected:
  //protected members go here
  private:
  //private members go here	
};
`}</pre></figure>
<hr className="my-5"/>
                        <h3 className="font-weight-bold mb-2">
                            public
                        </h3>
						<ul type="circle">
							<li>The public keyword is used to create public members (data and functions).</li>
							<li>The public members are accessible from any part of the program.</li>
						</ul>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Public Member Syntax -->`}</div>
                                    {`{
	public:    //Public access specifier
	int x;   // Public attribute
	private:   // Private access specifier
	int y;   // Private attribute
};
int main() {
	MyClass myObj;
	myObj.x = 25;  // Allowed (public)
	myObj.y = 50;  // Not allowed (private)
	return 0;
}`}</pre></figure>
<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Public Member Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Circle 
{ 
  public: 
    double radius;
    double compute_area() 
    { 
       return 3.14*radius*radius; 
    } 
}; 
void main() 
{ 
   clrscr();
   Circle obj; 
   // accessing public datamember outside class 
   obj.radius = 5.5; 
   cout << "Radius is: " << obj.radius << "\\n"; 
   cout << "Area is: " << obj.compute_area(); 
   getch();
} 
`}</pre></figure>
<hr className="my-5"/>
                        <h3 className="font-weight-bold mb-2">
                            private 
                        </h3>
						<ul type="circle">
							<li>The private keyword is used to create private members (data and functions).</li>
							<li>The private members can be accessed only from within the class.</li>
							<li>However, friend classes and friend functions can access private members.</li>
						</ul>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Private Member Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Circle 
{ 
  private:
    double radius;
  public:
    void compute_area(double r) 
    {   // member function can access private
      radius = r;
      double area = 3.14*radius*radius;
      cout << "Radius is: " << radius << endl; 
      cout << "Area is: " << area; 
    }
}; 
void main() 
{
  Circle obj; 
  obj.compute_area(1.5); 
  getch();
} 
`}</pre></figure>
<hr className="my-5"/>
                        <h3 className="font-weight-bold mb-2">
                            protected Access Modifier 
                        </h3>
						<ul type="circle">
							<li>The protected keyword is used to create protected members (data and function).</li>
							<li>The protected members can be accessed within the class and from the derived class.</li>
						</ul>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Protected Member Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Parent 
{ 
  protected: 
   int id_protected;
}; 
class Child : public Parent 
{ 
  public: 
  void setId(int id) 
  { 
    id_protected = id;
  } 
  void displayId() 
  { 
    cout << "id_protected is: " << id_protected << endl; 
  } 
}; 
void main() { 
  Child obj1;
  obj1.setId(81); 
  obj1.displayId(); 
  getch();
} 
`}</pre></figure>
                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}