import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import features from '../../../Images/data/c++/features.png';

export default function CCFeatures() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Features
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Features of C++ Language</h1>
                            <p className="lead mb-3"> C is the widely used language. It provides many features that are given below. </p>
                            <ol>
                                <li>Simple</li>
                                <li>Machine Independent or Portable</li>
                                <li>Mid-level programming language</li>
                                <li>structured programming language</li>
                                <li>Rich Library</li>
                                <li>Memory Management</li>
                                <li>Fast Speed</li>
                                <li>Pointers</li>
                                <li>Recursion</li>
                                <li>Extensible</li>
                                <li>Object Oriented</li>
                                <li>Compiler based</li>
                            </ol>
                            <center><img src={features} width="90%" alt="Features"/></center>
                            <ol>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Simple</li>
                                </h3>
                                <p>C++ is a simple language in the sense that it provides structured approach (to break the problem into parts), rich set of library functions, data types etc.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Machine Independent or Portable</li>
                                </h3>
                                <p>Unlike assembly language, c programs can be executed in many machines with little bit or no change. But it is not platform-independent.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Mid-level programming language</li>
                                </h3>
                                <p>C++ is also used to do low level programming. It is used to develop system applications such as kernel, driver etc. It also supports the feature of high level language. That is why it is known as mid-level language.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>structured programming language</li>
                                </h3>
                                <p>C++ is a structured programming language in the sense that we can break the program into parts using functions. So, it is easy to understand and modify.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Rich Library</li>
                                </h3>
                                <p>C++ provides a lot of inbuilt functions that makes the development fast.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Memory Management</li>
                                </h3>
                                <p>It supports the feature of dynamic memory allocation. In C++ language, we can free the allocated memory at any time by calling the free() function.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Fast Speed</li>
                                </h3>
                                <p>The compilation and execution time of C++ language is fast.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Pointers</li>
                                </h3>
                                <p>C++ provides the feature of pointers. We can directly interact with the memory by using the pointers. We can use pointers for memory, structures, functions, array etc.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Recursion</li>
                                </h3>
                                <p>In C++, we can call the function within the function. It provides code reusability for every function.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Extensible</li>
                                </h3>
                                <p>C++ language is extensible because it can easily adopt new features.	</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Object Oriented</li>
                                </h3>
                                <p>C++ is object oriented programming language. OOPs makes development and maintenance easier where as in Procedure-oriented programming language it is not easy to manage if code grows as project size grows.</p>
                                <hr className="my-5" />
                                <h3 className="font-weight-bold mb-2">
                                    <li>Compiler based</li>
                                </h3>
                                <p>C++ is a compiler based programming language, it means without compilation no C++ program can be executed. First we need to compile our program using compiler and then we can execute our program.</p>
                            </ol>
                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
