import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import ForImg from '../../../Images/data/c++/for.jpg';
import WhileImg from '../../../Images/data/c++/while.jpg';
import DowhileImg from '../../../Images/data/c++/do.jpg';

export default function CCLoop() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Loops
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                    <article className="main-content col-md-9 pr-lg-5">
                        
                    <h1 className="mb-2 mt-3">Loops</h1>
                        
                        <p>In loop, a loop is a sequence of instructions that is repeated until a certain condition is reached.</p>
						<p>There are mainly two types of loops:</p>
						<ul>
							<li>Entry Controlled loops: In this type of loops the test condition is tested before entering the loop body. For Loop and While Loop are entry controlled loops.</li>
							
							<li>Exit Controlled Loops: In this type of loops the test condition is tested or evaluated at the end of loop body. Therefore, the loop body will execute atleast once, irrespective of whether the test condition is true or false. do – while loop is exit controlled loop.</li>
						</ul>
                        <hr className="my-5"/>
						<h1 className="mb-2 mt-3">for Loop</h1>
                        <p>In for loop, a loop variable is used to control the loop. First initialize this loop variable to some value, then check whether this variable is less than or greater than counter value. If statement is true, then loop body is executed and loop variable gets updated . Steps are repeated till exit condition comes.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`for (initialization; condition; incr/decr)
{    
     // body of the loop
     // statements we want to execute
}`}</pre></figure>
						<img src={ForImg} alt="For"/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() 						
{ 
    int i=0;   
    for (i = 1; i <= 5; i++) 
    { 
        cout<<i <<"\\n";      

    } 
    getch(); 
} 
Output:

1
2
3
4
5`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">While Loop</h1>
                        <p>A while loop statement repeatedly executes a target statement as long as a given condition is true.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`initialization expression;
while (test_expression)
{
   // statements
 
  update_expression;
}
`}</pre></figure>
						<img src={WhileImg} alt="While"/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() {
    int i = 1; 
    // test expression 
    while (i < 6) 
    { 
        cout<<"Hello World"<<endl;     
  
        // update expression 
        i++; 
    } 

  getch(); 
}
 
Output:

Hello World
Hello World
Hello World
Hello World
Hello World`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">do while loop</h1>
                        <p>In do while loops also the loop execution is terminated on the basis of test condition. The main difference between do while loop and while loop is in do while loop the condition is tested at the end of loop body, i.e do while loop is exit controlled whereas the other two loops are entry controlled loops.</p>
						<p><b>Note:</b> In do while loop the loop body will execute at least once irrespective of test condition..</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`initialization expression;
do
{
   // statements

   update_expression;
} while (test_expression);
						
`}</pre></figure>
						<img src={DowhileImg} alt="Dowhile"/>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() 						
{ 
    clrscr();
    int i = 2; // Initialization expression 
    do
    { 
        // loop body 
        cout<<"Hello World"<<endl;     
  
        // update expression 
        i++; 
  
    }  while (i < 1);   // test expression 
    getch(); 
} 
Output:

Hello World`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
