import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Math() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Math
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                        <h1 className="mb-2 mt-3">Math Function</h1>
                        
                        <p>The math.h header defines various mathematical functions and one macro. All the functions available in this library take double as an argument and return double as the result. Let us discuss some important functions one by one.</p>
						<table
  style={{
    width: "100%",
    border: "2px solid skyblue",
    borderCollapse: "collapse",
  }}
>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ padding: "5px 0px 5px 10px", border: "1px solid skyblue" }}>
        SNO
      </th>
      <th style={{ padding: "5px 0px 5px 10px", border: "1px solid skyblue" }}>
        Function
      </th>
      <th style={{ padding: "5px 0px 5px 10px", border: "1px solid skyblue" }}>
        Description
      </th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>1</td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        ceil(number)
      </td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        rounds up the given number. It returns the integer value which is greater than or equal to given number.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>2</td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        floor(number)
      </td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        rounds down the given number. It returns the integer value which is less than or equal to given number.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>3</td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        sqrt(number)
      </td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        returns the square root of given number.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>4</td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        pow(base, exponent)
      </td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        returns the power of given number.
      </td>
    </tr>
    <tr>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>5</td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        abs(number)
      </td>
      <td style={{ padding: "3px 0px 3px 10px", border: "1px solid skyblue" }}>
        returns the absolute value of given number.
      </td>
    </tr>
  </tbody>
</table>
	
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<math.h> 
void main() {
  printf("\\n%f",ceil(3.6));    
  printf("\\n%f",floor(3.6));     
  printf("\\n%f",sqrt(16));    
  printf("\\n%f",pow(2,4));    
  printf("\\n%d",abs(-12));     
  getch(); 
}
 
Output:

4.000000
3.000000
4.000000
16.000000
12`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}

