import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Polymorphism() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Polymorphism
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Polymorphism</h1>
                            <p>When one task is performed by different ways i.e. known as polymorphism. we use Function overloading and Function overriding to achieve polymorphism.</p>
                            <p>In polymorphism is mainly divided into two types:</p>
                            <ul>
                                <li>Compile time Polymorphism</li>
                                <li>Runtime Polymorphism</li>
                            </ul>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Compile time polymorphism
                            </h3>
                            <p>This type of polymorphism is achieved by function overloading or operator overloading.</p>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Function Overloading
                            </h3>
                            <p>When there are multiple functions with same name but different parameters then these functions are said to be overloaded. Functions can be overloaded by change in number of arguments or/and change in type of arguments.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class A
{ 
    public: 
    void func(int x) 
    { 
        cout << "value of x is " << x << endl; 
    } 
    void func(double x) 
    { 
        cout << "value of x is " << x << endl; 
    } 
    void func(int x, int y) 
    { 
        cout << "value of x and y is " << x << ", " << y << endl; 
    } 
}; 
void main() { 
    A obj1; 
    obj1.func(7); 
    obj1.func(9.132); 
    obj1.func(85,64); 
    getch(); 
}  

Output:

value of x is 7
value of x is 9.132
value of x and y is 85, 64

`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Operator Overloading
                            </h3>
                            <p>we can make the operator (‘+’) for string class to concatenate two strings. We know that this is the addition operator whose task is to add two operands. So a single operator ‘+’ when placed between integer operands , adds them and when placed between string operands, concatenates them.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Complex { 
  private: 
   int real, imag; 
  public: 
    Complex(int r = 0, int i =0)  {real = r;   imag = i;} 
    Complex operator + (Complex const &obj) { 
      Complex res; 
      res.real = real + obj.real; 
      res.imag = imag + obj.imag; 
      return res; 
    } 
    void print() { 
      cout << real << " + i" << imag << endl; 
    } 
}; 
void main() 
{ 
  Complex c1(10, 5), c2(2, 4); 
  Complex c3 = c1 + c2; // "operator+" 
  c3.print(); 
} 
       
Output:
    
12 + i9    
       
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Runtime polymorphism:
                            </h3>
                            <ol>
                                <li>Virtual Functions</li>
                                <li>Pure virtual functions</li>
                                <li>Function overloading</li>
                            </ol>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Virtual Function
                            </h3>
                            <p>A virtual function a member function which is declared within a base class and is re-defined(Overriden) by a derived class. When you refer to a derived class object using a pointer or a reference to the base class, you can call a virtual function for that object and execute the derived class’s version of the function.</p>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class base { 
  public: 
    virtual void print() 
    { 
      cout << "print base class" << endl; 
    }    
    void show() 
    { 
      cout << "show base class" << endl; 
    } 
}; 
class derived : public base { 
  public: 
    void print() 
    { 
      cout << "print derived class" << endl; 
    } 
    void show() 
    { 
      cout << "show derived class" << endl; 
    } 
}; 
int main() 
{ 
  base* bptr; 
  derived d; 
  bptr = &d; 
  // virtual function, binded at runtime 
  bptr->print(); 
  // Non-virtual function, binded at compile time 
  bptr->show(); 
}   

`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="font-weight-bold mb-2">
                            Pure Virtual Function
                            </h1>
                            <p>Similarities between virtual function and pure virtual function:</p>
						<ol>
							<li>These are the concepts of Run-time polymorphism</li>
							<li>Prototype i.e. Declaration of both the functions remains the same throughout the program.</li>
							<li>These functions can’t be global or static.</li>
						</ol>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Class Syntax -->`}</div>
                                    {`//virtual void display()=0;
`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example 1 -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class Base 
{ 
   int x; 
public: 
    virtual void fun() = 0; 
    int getX() { return x; } 
}; 
  
class Derived: public Base 
{ 
    int y; 
public: 
    void fun() { cout << "fun() called"; } 
}; 
  
void main() 
{ 
    Derived d; 
    d.fun(); 
    getch(); 
} 

Output:

fun() called

`}</pre></figure>
<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example 2 -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
class B {
    public:
       virtual void s() = 0; // Pure Virtual Function
 };
 
 class D:public B {
    public:
       void s() {
          cout << " Virtual Function in Derived class\\n";
       }
 };
 
 void main() {
    B *b;
    D dobj; // An object of class D
    b = &dobj;// A pointer of type B* pointing to dobj
    b->s();// prints"D::s() called"
 }
 

`}</pre></figure>

                            <hr className="my-5" />

                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}