import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpArrayFunc() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Array Function
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>
                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Array Function</h1>
                            <h3 className="mb-2 mt-3">array() function</h3>
                            <p>PHP array() function creates and returns an array. It allows you to create indexed, associative and multidimensional arrays.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`array array ([ mixed $... ] )  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$season=array("summer","winter","spring","autumn");    
echo "Season are: $season[0], $season[1], $season[2] and $season[3]";    
?>

Output:

Season are: summer, winter, spring and autumn
 
`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">array_change_key_case() function</h1>
                            <p>PHP array_change_key_case() function changes the case of all key of an array.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`array array_change_key_case ( array $array [, int $case = CASE_LOWER ] )  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$salary=array("Sonoo"=>"550000","Vimal"=>"250000","Ratan"=>"200000");    
print_r(array_change_key_case($salary,CASE_UPPER));   
?>

Output:

Array ( [SONOO] => 550000 [VIMAL] => 250000 [RATAN] => 200000 )`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">array_chunk() function</h1>
                            <p>PHP array_chunk() function splits array into chunks. By using array_chunk() method, you can divide array into many parts.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`array array_chunk ( array $array , int $size [, bool $preserve_keys = false ] )   `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$salary=array("Sonoo"=>"550000","Vimal"=>"250000","Ratan"=>"200000");    
print_r(array_chunk($salary,2));   
?>

Output:

Array ( 
[0] => Array ( [0] => 550000 [1] => 250000 ) 
[1] => Array ( [0] => 200000 )
)
 `}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="mb-2 mt-3">count() function</h3>
                            <p>PHP count() function counts all elements in an array.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`int count ( mixed $array_or_countable [, int $mode = COUNT_NORMAL ] ) `}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$season=array("summer","winter","spring","autumn");    
echo count($season);    
?>

Output:

4`}</pre></figure>

                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">sort() function</h1>
                            <p>PHP sort() function sorts all the elements in an array.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`bool sort ( array &$array [, int $sort_flags = SORT_REGULAR ] )`}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$season=array("summer","winter","spring","autumn");    
sort($season);  
foreach( $season as $s )    
{    
  echo "$s<br />";    
}    
?>

Output:

autumn
spring
summer
winter`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">array_reverse() function</h1>
                            <p>PHP array_reverse() function returns an array containing elements in reversed order.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`array array_reverse ( array $array [, bool $preserve_keys = false ] )`}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$season=array("summer","winter","spring","autumn");    
$reverseseason=array_reverse($season);  
foreach( $reverseseason as $s )    
{    
  echo "$s<br />";    
}   
?>

Output:

autumn
spring
winter
summer`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="mb-2 mt-3">array_search() function</h3>
                            <p>PHP array_search() function searches the specified value in an array. It returns key if search is successful.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`mixed array_search ( mixed $needle , array $haystack [, bool $strict = false ] )  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$season=array("summer","winter","spring","autumn");    
$key=array_search("spring",$season);  
echo $key;    

?>

Output:

2`}</pre></figure>

                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">array_intersect() function</h1>
                            <p>PHP array_intersect() function returns the intersection of two array. In other words, it returns the matching elements of two array.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`array array_intersect ( array $array1 , array $array2 [, array $... ] )  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$name1=array("sonoo","john","vivek","smith");    
$name2=array("umesh","sonoo","kartik","smith");    
$name3=array_intersect($name1,$name2);  
foreach( $name3 as $n )    
{    
  echo "$n<br />";    
}    
?>

Output:

sonoo
smith`}</pre></figure>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
