import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function CCFunctions() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Functions
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Function</h1>

                            <p>A function is a set of statements that take inputs, do some specific computation and produces output. The idea is to put some commonly or repeatedly done task together and make a function so that instead of writing the same code again and again for different inputs, we can call the function.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`return_type function_name([ arg1_type arg1_name, ... ]) { code }   `}</pre></figure>
                            <p>
                                A C++ function definition consists of a function header and a function body. Here are all the parts of a function −
                            </p>
                            <ul>
                                <li><b>Return Type −</b> A function may return a value. The return_type is the data type of the value the function returns. Some functions perform the desired operations without returning a value. In this case, the return_type is the keyword void.</li>
                                <li><b>Function Name −</b> This is the actual name of the function. The function name and the parameter list together constitute the function signature.</li>
                                <li><b>Parameters −</b> A parameter is like a placeholder. When a function is invoked, you pass a value to the parameter. This value is referred to as actual parameter or argument. The parameter list refers to the type, order, and number of the parameters of a function. Parameters are optional; that is, a function may contain no parameters.</li>
                                <li><b>Function Body −</b> The function body contains a collection of statements that define what the function does.</li>
                            </ul>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Function Aspects</h1>
                            <ol>
                                <li><b>Function declaration:</b> A function must be declared globally in a c program to tell the compiler about the function name, function parameters, and return type.</li>
                                <li><b>Function call</b> Function can be called from anywhere in the program. The parameter list must not differ in function calling and function declaration. We must pass the same number of functions as it is declared in the function declaration.</li>
                                <li><b>Function definition</b> It contains the actual statements which are to be executed. It is the most important aspect to which the control comes when the function is called. Here, we must notice that only one value can be returned from the function.</li>
                            </ol>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
int max(int x, int y)  
{  
  if (x > y)  
  return x;  
  else
  return y;  
}    
int main() { 
  int a = 10, b = 20;  
  // Calling above function to find max of 'a' and 'b'  
  int m = max(a, b);  
  cout << "m is " << m;  
  return 0;  
} 

Output:

m is 20`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Function Declaration and Definition</h1>
                        <p>A C++ function consist of two parts:</p>
						<ul>
							<li><b>Declaration:</b> the function's name, return type, and parameters (if any)</li>
							<li><b>Definition:</b> the body of the function (code to be executed)</li> 
						</ul>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- declartion & definition -->`}</div>
                                    {`void myFunction() { // declaration
  // the body of the function (definition)
}
 `}</pre></figure>
 <hr className="my-5"/>
						<h1 className="mb-2 mt-3">Types of Functions</h1>
                        <p>There are two types of functions in C++ programming:</p>
						<ol>
							<li>Standard Library Functions: Predefined in C++</li>
							<li>User-defined functions: Created by users</li> 
						</ol>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Standard Library Functions</h1>
                        <p>Library functions are the built-in functions in C++ programming. Programmers can use library functions by invoking the functions directly; they don't need to write the functions themselves. Some common library functions in C++ are sqrt(), abs(), isdigit(), etc. In order to use library functions, we usually need to include the header file in which these library functions are defined. For instance, in order to use mathematical functions such as sqrt() and abs(), we need to include the header file cmath.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
#include<cmath.h> 
void main() { 
  double number, squareRoot;
  number = 25.0;
  // sqrt() is a library function to calculate the square root
  squareRoot = sqrt(number);
  cout << "Square root of " << number << " = " << squareRoot;
  getch();
} 

Output:

Square root of 25 = 5
`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">User-defined functions</h1>
                        <p>C++ allows the programmer to define their own function. A user-defined function groups code to perform a specific task and that group of code is given a name (identifier). When the function is invoked from any part of the program, it all executes the codes defined in the body of the function.</p>
						 
						<hr className="my-5"/>
						<p>There are two ways that a C++ function can be called from a program. They are,</p>
						<ol>	
							<li>Call by value</li>
							<li>Call by reference</li>
						</ol>
						<h3 className="mb-2 mt-3">Call By Value</h3>
						<p>The call by value method of passing arguments to a function copies the actual value of an argument into the formal parameter of the function. In this case, changes made to the parameter inside the function have no effect on the argument. By default, C++ uses call by value to pass arguments. In general, this means that code within a function cannot alter the arguments used to call the function.</p>
						

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void swap(int x, int y) {
  int temp;
  temp = x; /* save the value of x */
  x = y;    /* put y into x */
  y = temp; /* put x into y */
  return;
} 
void main () {
  // local variable declaration:
  int a = 100;
  int b = 200;  
  cout << "Before swap, value of a :" << a << endl;
  cout << "Before swap, value of b :" << b << endl;
  // calling a function to swap the values.
  swap(a, b);
  cout << "After swap, value of a :" << a << endl;
  cout << "After swap, value of b :" << b << endl;
getch();
}
 
Output:
 
Before swap, value of a :100
Before swap, value of b :200
After swap, value of a :100
After swap, value of b :200
`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="mb-2 mt-3">CALL BY REFERENCE:</h3>
                        <p>The call by reference method of passing arguments to a function copies the reference of an argument into the formal parameter. Inside the function, the reference is used to access the actual argument used in the call. This means that changes made to the parameter affect the passed argument. To pass the value by reference, argument reference is passed to the functions just like any other value.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void swap(int &x, int &y) {
  int temp;
  temp = x; /* save the value at address x */
  x = y;    /* put y into x */
  y = temp; /* put x into y */
  return;
}
void main()
{
  int a = 100;
  int b = 200;
  cout << "Before swap, value of a :" << a << endl;
  cout << "Before swap, value of b :" << b << endl;
  /* calling a function to swap the values using variable reference.*/
  swap(a, b);
  cout << "After swap, value of a :" << a << endl;
  cout << "After swap, value of b :" << b << endl;
getch();	
}
 
Output:
 
Before swap, value of a :100
Before swap, value of b :200
After swap, value of a :200
After swap, value of b :100`}</pre></figure>
<hr className="my-5"/>
						<h3 className="mb-2 mt-3">CALL BY POINTER:</h3>
                        <p>The call by pointer method of passing arguments to a function copies the address of an argument into the formal parameter. Inside the function, the address is used to access the actual argument used in the call. This means that changes made to the parameter affect the passed argument. To pass the value by pointer, argument pointers are passed to the functions just like any other value. </p>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void swap(int *x, int *y) {
  int temp;
  temp = *x; /* save the value at address x */
  *x = *y; /* put y into x */
  *y = temp; /* put x into y */
   
  return;
}
void main()
{
  int a = 100;
  int b = 200;  
  cout << "Before swap, value of a :" << a << endl;
  cout << "Before swap, value of b :" << b << endl;
  /* calling a function to swap the values using variable reference.*/
  swap(&a, &b);
  cout << "After swap, value of a :" << a << endl;
  cout << "After swap, value of b :" << b << endl;
  getch();	
} 
 
Output:
 
Before swap, value of a :100
Before swap, value of b :200
After swap, value of a :200
After swap, value of b :100`}</pre></figure>
                            <hr className="my-5" />
                            <h3 className="mb-2 mt-3">Function Prototype:</h3>
                            <p>An C++, the code of function declaration should be before the function call. However, if we want to define a function after the function call, we need to use the function prototype. For example, </p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`returnType functionName(dataType1, dataType2, ...);`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
int add(int, int);
int main() {
    int sum;
    // calling the function and storing
    // the returned value in sum
    sum = add(100, 78);
    cout << "100 + 78 = " << sum << endl;
    return 0;
}

// function definition
int add(int a, int b) {
    return (a + b);
}

Output:

100 + 78 = 178`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
