import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import ife from '../../../Images/data/c++/if-else.png';
import ifelseif from '../../../Images/data/c++/if-else1.png';
import ife1 from '../../../Images/data/c++/else-if.png';

export default function PhpIfelse() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Decision Making
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Decision Making</h1>
                            <p>PHP allows us to perform actions based on some type of conditions that may be logical or comparative. Based on the result of these conditions i.e., either TRUE or FALSE, an action would be performed as asked by the user. It’s just like a two- way path. If you want something then go this way or else turn that way. To use this feature, PHP provides us with four conditional statements:</p>
                            <ol>
                                <li>if statement</li>
                                <li>if-else statement</li>
                                <li>if-else-if statement</li>
                                <li>Switch statement</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">If statement</h1>
                            <p>This statement allows us to set a condition. On being TRUE, the following block of code enclosed within the if clause will be executed.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition){    
    // if TRUE then execute this code
}`}</pre></figure>
                            <br />
                            <img src={ife} alt="If"/>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$x = 12; 
  
if ($x > 0) { 
    echo "The number is positive"; 
} 
?>

Output:

The number is positive`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">If else statement</h1>
                            <p>We understood that if a condition will hold i.e., TRUE, then the block of code within if will be executed. But what if the condition is not TRUE and we want to perform an action? This is where else comes into play. If a condition is TRUE then if block gets executed, otherwise else block gets executed.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if(condition) {
      // if TRUE then execute this code
}
else {
     // if FALSE then execute this code

}
`}</pre></figure>
                            <img src={ife1} alt="Ife"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$x = -12; 
  
if ($x > 0) { 
    echo "The number is positive"; 
} 
  
else{ 
    echo "The number is negative"; 
} 
?>

Output:

The number is negative

 `}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">if-else-if ladder Statement</h1>
                            <p>This allows us to use multiple if…else statments. We use this when there are multiple conditions of TRUE cases.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`if (condition) {
    // if TRUE then execute this code
}
elseif {
    // if TRUE then execute this code
}
elseif {
    // if TRUE then execute this code
}
else {
    // if FALSE then execute this code
}
`}</pre></figure>
                            <br />
                            <img src={ifelseif} alt="IfElseIf"/>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
$x = "August"; 
  
if ($x == "January") { 
    echo "Happy Republic Day"; 
} 
  
elseif ($x == "August") { 
    echo "Happy Independence Day!!!"; 
} 
  
else{ 
    echo "Nothing to show"; 
} 
?>

Output:

Happy Independence Day!!!
 `}</pre></figure>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
