import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Cookies() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Cookies
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Cookies</h1>
                            <p>PHP cookie is a small piece of information which is stored at client browser. It is used to recognize the user.</p>
                            <p>Cookie is created at server side and saved to client browser. Each time when client sends request to the server, cookie is embedded with request. Such way, cookie can be received at the server side.</p>
                            <p>In short, cookie can be created, sent and received at server end.</p>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">setcookie() function</h1>
                            <p>PHP setcookie() function is used to set cookie with HTTP response. Once cookie is set, you can access it by $_COOKIE superglobal variable.</p>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`setcookie(name, value, expire, path, domain, secure);
                                    `}
                                </pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`setcookie("CookieName", "CookieValue");/* defining name and value only*/  
setcookie("CookieName", "CookieValue", time()+1*60*60);//using expiry in 1 hour(1*60*60 seconds or 3600 seconds)  
setcookie("CookieName", "CookieValue", time()+1*60*60, "/mypath/", "mydomain.com", 1);  
                                    `}
                                </pre></figure>
                            <table style={{ borderCollapse: "collapse", width: "100%", border: "2px solid skyblue" }}>
                                <thead>
                                    <tr style={{ backgroundColor: "black", color: "white" }}>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>Parameter</th>
                                        <th style={{ padding: "5px 10px", textAlign: "left", border: "1px solid skyblue" }}>Description</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>name</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>The name of the cookie.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>value</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>The value of the cookie. Do not store sensitive information since this value is stored on the user's computer.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>expires</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>The expiry date in UNIX timestamp format. After this time cookie will become inaccessible. The default value is 0.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>path</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Specify the path on the server for which the cookie will be available. If set to /, the cookie will be available within the entire domain.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>domain</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>Specify the domain for which the cookie is available to e.g www.example.com.</td>
                                    </tr>
                                    <tr>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>secure</td>
                                        <td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>This field, if present, indicates that the cookie should be sent only if a secure HTTPS connection exists.</td>
                                    </tr>
                                </tbody>
                            </table>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">$_COOKIE</h1>
                            <p>PHP $_COOKIE superglobal variable is used to get cookie.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`$value=$_COOKIE["CookieName"];//returns cookie value  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php 
setcookie("user", "Sonoo");  
?>

<html>  
<body>  
<?php  
if(!isset($_COOKIE["user"])) {  
    echo "Sorry, cookie is not found!";  
} else {  
    echo "<br/>Cookie Value: " . $_COOKIE["user"];  
}  
?>
</body>  
</html> 
 
Output:

Sorry, cookie is not found!
  
//Firstly cookie is not set. But, if you refresh the page, you will see cookie is set now.

Cookie Value: Sonoo
                                    `}
                                </pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Delete Cookie</h1>
                            <p>If you set the expiration date in past, cookie will be deleted.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
setcookie ("CookieName", "", time() - 3600);// set the expiration date to one hour ago  
?>`}</pre></figure>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
