import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function CCVariable() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Variables
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Variables</h1>

                            <p>A variable is a name of the memory location. It is used to store data. Its value can be changed, and it can be reused many times. It is a way to represent memory location through symbol so that it can be easily identified.</p>
                            <h3 className="mb-2 mt-3">Rules for defining variables</h3>
                            <ul>
                                <li>A variable can have alphabets, digits, and underscore.</li>
                                <li>A variable name can start with the alphabet, and underscore only. It can't start with a digit.</li>
                                <li>No whitespace is allowed within the variable name.</li>
                                <li>A variable name must not be any reserved word or keyword, e.g. int, float, etc.</li>
                            </ul>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`type variable_list;
                                    `}

                                    <div className="text-muted mb-2">{`<!-- Example -->`}</div>

                                    {`int a;  
float b;  
char c;`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Types of Variables</h1>
                            <p>Variables can be categorised based on their data type. For example, in the above example we have seen integer types variables. Following are the types of variables available in C++.</p>
                            <ol>
                                <li><b>int:</b> These type of of variables holds integer value.</li>
                                <li><b>char:</b> holds character value like ‘c’, ‘F’, ‘B’, ‘p’, ‘q’ etc.</li>
                                <li><b>bool:</b> holds boolean value true or false.</li>
                                <li><b>double:</b> double-precision floating point value.</li>
                                <li><b>float:</b> Single-precision floating point value.</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Types of variables based on their scope</h1>
                            <ol>
                                <li>local variable</li>
                                <li>global variable</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Local Variable</h1>
                            <p>Local variables are declared inside the braces of any user defined function, main function, loops or any control statements(if, if-else etc) and have their scope limited inside those braces.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() 						
{ 
    int x=10;//local variable  
    cout << "x =" << x; 
    getch(); 
} 
Output:

x = 10`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Global Variable</h1>
                            <p>A variable declared outside of any function (including main as well) is called global variable. Global variables have their scope throughout the program, they can be accessed anywhere in the program, in the main, in the user defined function, anywhere. </p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
char myVar = 'A';
void main(){
   cout << "Value of myVar: " << myVar << endl;
   myVar='Z';
   cout << "Value of myVar: " << myVar;
   getch(); 
}

Output:

Value of myVar: A
Value of myVar: Z`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
