import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpIntroduction() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Introduction
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">
                            <h1 className="mb-2 mt-3">Introduction</h1>
                            <p>PHP is an open-source, interpreted, and object-oriented scripting language that can be executed at the server-side. PHP is well suited for web development. Therefore, it is used to develop web applications (an application that executes on the server and generates the dynamic page.).</p>
                            <p>PHP was created by Rasmus Lerdorf in 1994 but appeared in the market in 1995. PHP 7.4.0 is the latest version of PHP, which was released on 28 November. Some important points need to be noticed about PHP are as followed:</p>
                            <ul>
                                <li>PHP stands for Hypertext Preprocessor..</li>

                                <li>PHP is an interpreted language, i.e., there is no need for compilation.</li>

                                <li>PHP is faster than other scripting languages, for example, ASP and JSP.</li>

                                <li>PHP is a server-side scripting language, which is used to manage the dynamic content of the website.</li>

                                <li>PHP can be embedded into HTML.</li>

                                <li>PHP is an object-oriented language.</li>

                                <li>PHP is an open-source scripting language.</li>
                                <li>PHP is simple and easy to learn language.</li>
                            </ul>
                            <hr className="my-5" />

                            <p className="lead mb-3">What is the use of php</p>
                            <p>PHP is a server-side scripting language, which is used to design the dynamic web applications with MySQL database.</p>
                            <ul type="circle">
                                <li>It handles dynamic content, database as well as session tracking for the website.</li>
                                <li>You can create sessions in PHP.</li>
                                <li>It can access cookies variable and also set cookies.</li>
                                <li>It helps to encrypt the data and apply validation.</li>
                                <li>PHP supports several protocols such as HTTP, POP3, SNMP, LDAP, IMAP, and many more.</li>
                                <li>Using PHP language, you can control the user to access some pages of your website.</li>
                                <li>As PHP is easy to install and set up, this is the main reason why PHP is the best language to learn.</li>
                                <li>PHP can handle the forms, such as - collect the data from users using forms, save it into the database, and return useful information to the user. For example - Registration form.</li>
                            </ul>
                            <hr className="my-5" />

                            <p className="lead mb-3">"Hello World" Script in PHP</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
{`<html>
	<head>
        <title>WebPage&lt;/title>
	</head>
	<body>
        <?php echo "hello world";  ?>
    </body>
</html>`}

</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Difference between client side scripting and server side scripting : </h1>
                        <table style={{ border: "2px solid skyblue", width: "100%" }}>
  <thead>
    <tr style={{ backgroundColor: "black", color: "white" }}>
      <th style={{ border: "1px solid skyblue", padding: "5px 0px 5px 10px" }}>Client Side Script</th>
      <th style={{ border: "1px solid skyblue", padding: "5px 0px 5px 10px" }}>Server Side Script</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>Source code is visible to user.</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        Source code is not visible to user because it’s output of server side is a HTML page.
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        It usually depends on browser and it’s version.
      </td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        In this any server side technology can be used and it does not depend on client.
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>It runs on user’s computer.</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>It runs on web server.</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        There are many advantages linked with this like faster response times, a more interactive application.
      </td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        The primary advantage is its ability to highly customize responses, requirements, and access rights based on user.
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>It does not provide security for data.</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>It provides more security for data.</td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        It is a technique used in web development in which scripts run on the client’s browser.
      </td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>
        It is a technique that uses scripts on the web server to produce a response that is customized for each client’s request.
      </td>
    </tr>
    <tr>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>HTML, CSS and JavaScript are used.</td>
      <td style={{ border: "1px solid skyblue", padding: "3px 0px 3px 10px" }}>PHP, Python, Java, Ruby are used.</td>
    </tr>
  </tbody>
</table>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    );
}
