import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function Structure() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Structure
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                        <h1 className="mb-2 mt-3">Structure</h1>
                        
                        <p>Structure is a user-defined datatype in C language which allows us to combine data of different types together. Structure helps to construct a complex data type which is more meaningful. It is somewhat similar to an Array, but an array holds data of similar type only. But structure on the other hand, can store data of any type, which is practical more useful.</p>
                        
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`struct structureName 
{
    dataType member1;
    dataType member2;
    ...
};`}

<div className="text-muted mb-2">{`<!-- Example -->`}</div>

{`struct Person
{
    char name[50];
    int citNo;
    float salary;
};`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Declaring structure variable</h1>
                        <p>We can declare a variable for the structure so that we can access the member of the structure easily. There are two ways to declare structure variable:</p>
						<ol>
							<li>By struct keyword within main() function</li>
							<li>By declaring a variable at the time of defining the structure.</li>
						</ol>	
						<h3 className="mb-2 mt-3">By struct keyword within main() function</h3>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`struct employee  
{   
    int id;  
    char name[50];  
    float salary;  
};
 
//  Now write given code inside the main() function.
 
struct employee e1, e2;  `}</pre></figure>
						<br/>
						<h3 className="mb-2 mt-3">By declaring a variable at the time of defining the structure.</h3>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`struct employee  
{   
    int id;  
    char name[50];  
    float salary;  
}e1,e2;`}</pre></figure>
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Accessing members of the structure</h1>
                        <p>There are two ways to access structure members:</p>
						<ol>
							<li>By . (member or dot operator)</li>
							<li>By -&gt; (structure pointer operator)</li>
						</ol>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
struct employee      
{   
  int id;      
  char name[50];      
}e1;  //declaring e1 variable for structure    
int main( )    
{    
  //store first employee information    
  e1.id=101;    
  strcpy(e1.name, "Sonoo Jaiswal");//copying string into char array    
  //printing first employee information    
  printf( "employee 1 id : %d\\n", e1.id);    
  printf( "employee 1 name : %s\\n", e1.name);    
  getch(); 
} 
Output:

employee 1 id : 101
employee 1 name : Sonoo Jaiswal`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">array of structures</h1>
                        <p>Like other primitive data types, we can create an array of structures.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
struct Point 
{ 
   int x, y; 
}; 
  
int main() 
{ 
   // Create an array of structures 
   struct Point arr[10]; 
   // Access array members 
   arr[0].x = 10; 
   arr[0].y = 20; 
   printf("%d %d", arr[0].x, arr[0].y); 
   return 0; 
} 

Output:

10 20`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Structure Pointer</h1>
                        <p>Like primitive types, we can have pointer to a structure. If we have a pointer to structure, members are accessed using arrow ( -&gt; ) operator.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
struct Point 
{ 
   int x, y; 
}; 
  
int main() 
{ 
   struct Point p1 = {1, 2}; 
   // p2 is a pointer to structure p1 
   struct Point *p2 = &p1; 
   // Accessing structure members using structure pointer 
   printf("%d %d", p2->x, p2->y); 
   return 0; 
}

Output:

1 2`}</pre></figure>                       
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">typedef in Structure</h1>
                        <p>typedef makes the code short and improves readability. In the above discussion we have seen that while using structs every time we have to use the lengthy syntax, which makes the code confusing, lengthy, complex and less readable. The simple solution to this issue is use of typedef. It is like an alias of struct.</p>
						<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`typedef struct Distance{
    int feet;
    float inch;
} distances;

int main() {
    distances d1, d2;
}
`}</pre></figure>  
<figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
typedef struct Books {
   char title[50];
   char author[50];
   char subject[100];
   int book_id;
} Book;
 
int main( ) {

   Book book;
 
   strcpy( book.title, "C Programming");
   strcpy( book.author, "Nuha Ali"); 
   strcpy( book.subject, "C Programming Tutorial");
   book.book_id = 6495407;
   printf( "Book title : %s\\n", book.title);
   printf( "Book author : %s\\n", book.author);
   printf( "Book subject : %s\\n", book.subject);
   printf( "Book book_id : %d\\n", book.book_id);
   return 0;
}

Output:

Book  title : C Programming
Book  author : Nuha Ali
Book  subject : C Programming Tutorial
Book  book_id : 6495407`}</pre></figure>  
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Nested Structure</h1>        
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
struct student_college_detail
{
    int college_id;
    char college_name[50];
};

struct student_detail 
{
    int id;
    char name[20];
    float percentage;
    // structure within structure
    struct student_college_detail clg_data;
}stu_data;

int main() 
{
    struct student_detail stu_data = {1, "Raju", 90.5, 71145,"Anna University"};
    printf("Id is: %d \\n", stu_data.id);
    printf("Name is: %s \\n", stu_data.name);
    printf("Percentage is: %f \\n\\n", stu_data.percentage);
    printf("College Id is: %d \\n",stu_data.clg_data.college_id);
    printf("College Name is: %s \\n",stu_data.clg_data.college_name);
    return 0;
}

Output:

Id is: 1
Name is: Raju
Percentage is: 90.500000
College Id is: 71145
College Name is: Anna University`}</pre></figure>          
						<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Passing Structure to function</h1>        
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
#include<string.h> 
struct student {
   char name[50];
   int age;
};

// function prototype
void display(struct student s);

int main() {
   struct student s1;
   printf("Enter name: ");
   // read string input from the user until \\n is entered
   // \\n is discarded
   scanf("%[^\\n]%*c", s1.name);
   printf("Enter age: ");
   scanf("%d", &s1.age);
   display(s1); // passing struct as an argument
   return 0;
}

void display(struct student s) {
   printf("\\nDisplaying information\\n");
   printf("Name: %s", s.name);
   printf("\\nAge: %d", s.age);
}

Output:

Enter name: Bond
Enter age: 13

Displaying information
Name: Bond
Age: 13`}</pre></figure>      
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
