import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpString() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        String
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">String</h1>

                            <p>PHP string is a sequence of characters i.e., used to store and manipulate text. PHP supports only 256-character set and so that it does not offer native Unicode support.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Declaration & Initialization -->`}</div>
                                    {`$string_1 = "This is a string in double quotes";
$string_2 = "This is a somewhat longer, singly quoted string";
$string_39 = "This string has thirty-nine characters";
$string_0 = ""; // a string with zero characters
`}</pre></figure>
                            <p>Singly quoted strings are treated almost literally, whereas doubly quoted strings replace variables with their values as well as specially interpreting certain character sequences.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
   $variable = "name";
   $literally = 'My $variable will not print!\\n';
   
   print($literally);
   print "<br />";
   
   $literally = "My $variable will print!\\n";
   
   print($literally);
?>

Output:

My $variable will not print!\n
My name will print!\n`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">String Functions</h1>
                            <ol>
                                <li>strtolower()</li>
                                <li>strtoupper()</li>
                                <li>strrev()</li>
                                <li>strlen()</li>
                                <li>strcpy()</li>
                                <li>strcmp()</li>
                                <li>strcat()</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strtolower()</h1>
                            <p>The strtolower() function returns string in lowercase letter.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`string strtolower ( string $string )  `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
	$str="My name is KHAN";  
	$str=strtolower($str);  
	echo $str;  

?>

Output:

my name is khan`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strtoupper()</h1>
                            <p>The strtoupper() function returns string in uppercase letter.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`string strtoupper ( string $string ) `}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
	$str="My name is KHAN";  
	$str=strtoupper($str);  
	echo $str;  
?>

Output:

MY NAME IS KHAN
`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strrev()</h1>
                            <p>The strrev() function returns reversed string.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`string strrev ( string $string )`}</pre></figure>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
	$str="my name is Sonoo jaiswal";  
	$str=strrev($str);  
	echo $str;
?>

Output:

lawsiaj oonoS si eman ym
`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strlen()</h1>
                            <p>The strlen() function is used to find the length of a string.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
   echo strlen("Hello world!");
?>

Output:

12`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcpy()</h1>
                            <p>Use it for copying a string into another</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`$str1 = $str2;`}</pre></figure>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
echo "Enter the key string: ";
$key = trim(fgets(STDIN)); // Read user input from console
// Copy string (strcpy equivalent)
$buffer = $key;
echo "Key = " . $key . PHP_EOL;
echo "Buffer = " . $buffer . PHP_EOL;
?>


Output:

Enter the key string: Php Tutorial
Key = Php Tutorial
Buffer = Php Tutorial

`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcmp()</h1>
                            <p>It compares two string.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`int strcmp(string $str1, string $str2)`}</pre></figure>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$key = "mango";

do {
    echo "What is my favourite fruit? ";
    $buffer = trim(fgets(STDIN)); 
} while (strcmp($key, $buffer) != 0); // strcmp returns 0 if both are equal

echo "Answer is correct!!" . PHP_EOL;
?>


Output:

What is my favourite fruit? apple
What is my favourite fruit? banana
What is my favourite fruit? mango
Answer is correct!!

`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcat()</h1>
                        <p>It appends one string at the end of another.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`int strcmp ( string $str1 , string $str2 )`}</pre></figure>
                            <br />
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$str1 = "Hello ";
$str2 = "World!";

// Concatenation in PHP
$result = $str1 . $str2;

echo "Concatenated String: " . $result . PHP_EOL;
?>

Output:

Concatenated String: Hello World!

`}</pre></figure>

                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
