import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function CCEvenodd() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Even or Odd
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">
                            <h1 className="mb-2 mt-3">Even or Odd</h1>
                            <ul className="nav nav-tabs" id="myTab" role="tablist">
                                <li className="nav-item">
                                    <a className="nav-link active" id="tri-tab" data-toggle="tab" href="#tri" role="tab" aria-controls="tri"
                                        aria-selected="true">With if-else</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" id="pira-tab" data-toggle="tab" href="#pira" role="tab" aria-controls="pira" aria-selected="false">With Conditional Operator</a>
                                </li>
                                <li className="nav-item">
                                    <a className="nav-link" id="hollow-tab" data-toggle="tab" href="#hollow" role="tab" aria-controls="hollow"
                                        aria-selected="false">With bitwise Operator</a>
                                </li>
                            </ul>
                            <div className="tab-content" id="myTabContent">
                                <br /><br />
                                <div className="tab-pane fade show active" id="tri" role="tabpanel" aria-labelledby="tri-tab"><h3>Even or Odd</h3>
                                    <figure className="code-block">
                                        <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                            {`#include<iostream.h>
#include<conio.h> 
void main() {
  int num;
  cout<<"Enter an integer: ";
  cin>>num;
  // Modulus (%) returns remainder
  if ( num%2 == 0 )
    cout<<"even number is "<<num;
  else
    cout<<"odd number is "<<num;
  getch(); 
}

Output:

Enter an integer: 33
33 is an odd number.`}</pre></figure>
                                </div>
                                <div className="tab-pane fade" id="pira" role="tabpanel" aria-labelledby="pira-tab"><h3>Even or Odd</h3>
                                <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() {
  int num;
  cout<<"Enter an integer: ";
  cin>>num;
  (num % 2 == 0) ? cout<<"even is "<<num : cout<<"odd is "<<num;
  getch(); 
}

Output:

Enter an integer: 33
33 is odd.`}</pre></figure>
                                </div>
                                <div className="tab-pane fade" id="hollow" role="tabpanel" aria-labelledby="Hollow-tab"><h3>Even or Odd</h3>
                                <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<iostream.h>
#include<conio.h> 
void main() {
  int n;
  cout<<"Input an integer\\n";
  cin>>n;
  if (n & 1)
    cout<<"Odd"<<"\\n";
  else
    cout<<"Even"<<"\\n";
  getch(); 
}
                                  
Output:

Input an integer 66
Even`}</pre></figure>
                                </div>
                            </div>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
