import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';
import ForImg from '../../../Images/data/c++/for.jpg';
import WhileImg from '../../../Images/data/c++/while.jpg';
import DowhileImg from '../../../Images/data/c++/do.jpg';

export default function PhpLoop() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Loops
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Loops</h1>

                            <p>Often when you write code, you want the same block of code to run over and over again in a row. Instead of adding several almost equal code-lines in a script, we can use loops to perform a task like this.</p>
                            <p>In PHP, we have the following looping statements:</p>
                            <ul>
                                <li>for - loops through a block of code a specified number of times</li>
                                <li>foreach - loops through a block of code for each element in an array</li>
                                <li>while - loops through a block of code as long as the specified condition is true</li>

                                <li>do...while - loops through a block of code once, and then repeats the loop as long as the specified condition is true</li>

                            </ul>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">for Loop</h1>
                            <p>The for loop repeats a block of code until a certain condition is met. It is typically used to execute a block of code for certain number of times.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`for (initialization; condition; incr/decr)
{    
     // Code to be executed
}`}</pre></figure>
                            <img src={ForImg} alt="For"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php

for($i=1; $i<=5; $i++){
    echo  $i."\\n";
}
?>

Output:

1
2
3
4
5`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">foreach Loop</h1>
                            <p>The foreach statement is used to loop through arrays. For each pass the value of the current array element is assigned to $value and the array pointer is moved by one and in the next pass next element will be processed.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`foreach (array as value)
{
code to be executed;
}
`}</pre></figure>
                            <img src={WhileImg} alt="While"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php

$fruits = array("mango", "apple", "papaya", "lichi");

foreach ($fruits as $value)
{
    echo "$value \\n";
}

?>

Output:

mango
apple
papaya
lichi`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">While Loop</h1>
                            <p>The while loop executes a block of code as long as the specified condition is true.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`while (condition is true)
{
   code to be executed;
}`}</pre></figure>
                            <img src={DowhileImg} alt="Dowhile"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$n = 1;

while($n <= 5) {
    echo "The number is: $n \\n";
    $n++;
}
?>

 
Output:

The number is: 1
The number is: 2
The number is: 3
The number is: 4
The number is: 5`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">do while loop</h1>
                            <p>The do…while statement will execute a block of code at least once – it then will repeat the loop as long as a condition is true.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`do {
code to be executed;
}
while (condition);`}</pre></figure>
                            <img src={DowhileImg} alt="DoWhile"/>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php
$n = 6;

do {
    echo "The number is: $n \\n";
    $n++;
} while ($n <= 5);
?>

Output:

The number is: 6`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
