import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function String() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        String
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">String</h1>

                            <p>The string can be defined as the one-dimensional array of characters terminated by a null ('\0'). The character array or the string is used to manipulate text such as word or sentences. Each character in the array occupies one byte of memory, and the last character must always be 0. The termination character ('\0') is important in a string since it is the only way to identify where the string ends. When we define a string as char s[10], the character s[10] is implicitly initialized with the null in the memory.</p>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Declaration & Initialization -->`}</div>
                                    {`char str_name[size];
char str[] = "Helloeveryone";
char str[50] = "Helloeveryone";
char str[] = {'H','e','l','l','o','e','v','e','r','y','o','n','e','\\0'};
char str[14] = {'H','e','l','l','o','e','v','e','r','y','o','n','e','\\0'};`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    char str[] = "Rahul"; 
    printf("%s",str); 
    getch(); 
} 

Output:

Rahul`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">fgets() and puts()</h1>
                            <p>You can use the fgets() function to read a line of string. And, you can use puts() to display the string.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() {
  char name[30];
  printf("Enter name: ");
  fgets(name, sizeof(name), stdin);  // read string
  printf("Name: ");
  puts(name);    // display string 
  getch(); 
}
 
Output:

Enter name: Tom Hanks
Name: Tom Hanks`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">String Functions</h1>
                            <ol>
                                <li>strlen()</li>
                                <li>strcpy()</li>
                                <li>strcmp()</li>
                                <li>strcat()</li>
                            </ol>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strlen()</h1>
                            <p>Find out the length of a string</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`size_t strlen(const char *str)
size_t represents unsigned short
It returns the length of the string without including end character (terminating char ‘\\0’).`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    clrscr();
    char str1[20] = "BeginnersBook";
    printf("Length of string str1: %d", strlen(str1));
    getch(); 
} 

Output:

Length of string str1: 13`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcpy()</h1>
                            <p>Use it for copying a string into another</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`char *strcpy( char *str1, char *str2)
It copies the string str2 into string str1, including the end character (terminator char ‘\\0’).`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    clrscr();
    char s1[30] = "string 1";
    char s2[30] = "string 2 : I’m gonna copied into s1";
    /* this function has copied s2 into s1*/
    strcpy(s1,s2);
    printf("String s1 is: %s", s1);
    getch(); 
} 
Output:

String s1 is: string 2: I’m gonna copied into s1`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcmp()</h1>
                            <p>It compares two string.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`int strcmp(const char *str1, const char *str2)`}</pre></figure>
                            <br />
                            <p>It compares the two strings and returns an integer value. If both the strings are same (equal) then this function would return 0 otherwise it may return a negative or positive value based on the comparison.</p>
                            <p>If string1 &lt; string2 OR string1 is a substring of string2 then it would result in a negative value. If string1 &gt; string2 then it would return positive value. If string1 == string2 then you would get 0(zero) when you use this function for compare strings.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    clrscr();
    char s1[20] = "Samrat";
    char s2[20] = "Samrat.com";
    if (strcmp(s1, s2) ==0)
    {
       printf("string 1 and string 2 are equal");
    }else
    {
       printf("string 1 and 2 are different");
    }
    getch(); 
} 
Output:

string 1 and 2 are different`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">strcat()</h1>
                            <p>It appends one string at the end of another.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`char *strcat(char *str1, char *str2)
It concatenates two strings and returns the concatenated string.`}</pre></figure>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
    clrscr();
    char s1[10] = "Hello";
    char s2[10] = "World";
    strcat(s1,s2);
    printf("Output string after concatenation: %s", s1);
    getch(); 
} 
Output:

Output string after concatenation: HelloWorld`}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">pass a string to a function</h1>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`#include<stdio.h>
#include<conio.h> 
void Strfun(char *ptr)
{
  printf("The String is : %s",ptr);
}
// main function
int main()
{
  // Declare a buffer of type "char"
  char buff[20]="Hello Function";
  // Passing string to Function
  Strfun(buff);
  return 0;
}

Output:

The String is : Hello Function`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
