import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function File() {
	const navigate = useNavigate();
	const [email, setEmail] = useState("");
	const [sidebarToggled, setSidebarToggled] = useState(false);
	const [sidebarPinned, setSidebarPinned] = useState(false);

	useEffect(() => {
		const storedEmail = sessionStorage.getItem("userEmail");
		if (!storedEmail) {
			navigate("/");
		} else {
			setEmail(storedEmail);
		}
	}, [navigate]);

	const handleLogout = () => {
		sessionStorage.removeItem("userEmail");
		navigate("/");
	};

	const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
	const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

	return (
		<div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
			<Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

			<main className="page-content">
				{sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

				<div className="container-fluid">
					{/* Top Bar */}
					<div className="row d-flex align-items-center p-3 border-bottom">
						<div className="col-md-1">
							<button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
								<i className="fas fa-bars"></i>
							</button>
						</div>
						<div className="col-md-8">
							<nav aria-label="breadcrumb">
								<ol className="breadcrumb d-none d-lg-inline-flex m-0">
									<li className="breadcrumb-item">
										<Link to="/dashboard">Home</Link>
									</li>
									<li className="breadcrumb-item active" aria-current="page">
										File Handling
									</li>
								</ol>
							</nav>
						</div>
						<div className="col-md-3 text-right">
							<button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
								{email ? `${email} (Logout)` : "Logout"}
							</button>
						</div>
					</div>
					{/* Main Content */}
					<div className="row p-lg-4">
						<article className="main-content col-md-9 pr-lg-5">

							<h1 className="mb-2 mt-3">File handling</h1>

							<p>In programming, we may require some specific input data to be generated several numbers of times. Sometimes, it is not enough to only display the data on the console. The data to be displayed may be very large, and only a limited amount of data can be displayed on the console, and since the memory is volatile, it is impossible to recover the programmatically generated data again and again. However, if we need to do so, we may store it onto the local file system which is volatile and can be accessed every time. Here, comes the need of file handling in C.</p>
							<p>File handling in C enables us to create, update, read, and delete the files stored on the local file system through our C program. The following operations can be performed on a file.</p>
							<ul>
								<li>Creation of the new file</li>
								<li>Opening an existing file</li>
								<li>Reading from the file</li>
								<li>Writing to the file</li>
								<li>Deleting the file</li>
							</ul>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Functions for file handling</h1>
							<p>There are many functions in the C library to open, read, write, search and close the file. A list of file functions are given below:</p>
							<table
								style={{
									width: "100%",
									border: "2px solid skyblue",
									borderCollapse: "collapse",
								}}
							>
								<thead>
									<tr style={{ backgroundColor: "black", color: "white" }}>
										<th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>SNO.</th>
										<th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>Function</th>
										<th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>Description</th>
									</tr>
								</thead>
								<tbody>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>1</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fopen()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											opens new or existing file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>2</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fprintf()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											write data into the file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>3</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fscanf()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											reads data from the file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>4</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fputc()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											writes a character into the file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>5</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fgetc()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											reads a character from file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>6</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fclose()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>closes the file</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>7</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fseek()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											sets the file pointer to given position
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>8</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fputw()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											writes an integer to file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>9</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>fgetw()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											reads an integer from file
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>10</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>ftell()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											returns current position
										</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>11</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>rewind()</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>
											sets the file pointer to the beginning of the file
										</td>
									</tr>
								</tbody>
							</table>

							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Opening File: fopen()</h1>
							<p>The fopen() function is used to create a new file or open an existing file in C. The fopen function is defined in the stdio.h header file. Now, lets see the syntax for creation of a new file or opening a file.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`file = fopen(“hello.txt”, “w”)`}</pre></figure>
							<br />
							<p>The fopen() function accepts two parameters:</p>
							<ul>
								<li>The file name (string). If the file is stored at some specific location, then we must mention the path at which the file is stored. For example, a file name can be like "c://some_folder/some_file.ext".</li>
								<li>The mode in which the file is to be opened. It is a string.</li>
							</ul>
							<p>We can use one of the following modes in the fopen() function.</p>
							<table
								style={{
									width: "100%",
									border: "2px solid skyblue",
									borderCollapse: "collapse",
								}}
							>
								<thead>
									<tr style={{ backgroundColor: "black", color: "white" }}>
										<th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>Mode</th>
										<th style={{ padding: "5px 10px", border: "1px solid skyblue" }}>Description</th>
									</tr>
								</thead>
								<tbody>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>r</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in read mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>w</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>a</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in append mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>r+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in read and write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>w+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in read and write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>a+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a text file in read and write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>rb</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in read mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>wb</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>ab</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in append mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>rb+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in read and write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>wb+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in read and write mode</td>
									</tr>
									<tr>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>ab+</td>
										<td style={{ padding: "3px 10px", border: "1px solid skyblue" }}>opens a binary file in read and write mode</td>
									</tr>
								</tbody>
							</table>

							<br />
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
	FILE *fp ;  
	char ch ;  
	fp = fopen("file_handle.txt","r") ;  
	while ( 1 )  
	{  
	ch = fgetc ( fp ) ;  
	if ( ch == EOF )  
	break ;  
	printf("%c",ch) ;  
	}  
	fclose (fp ) ;  
}  

Output:

File opened successfully in read mode
`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Closing File: fclose()</h1>
							<p>TThe fclose() function is used to close a file. The file must be closed after performing all the operations on it. The syntax of fclose() function is given below:</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fclose( FILE *fp );  
`}</pre></figure>

							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Writing File : fprintf() function</h1>
							<p>The fprintf() function is used to write set of characters into file. It sends formatted output to a stream.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fprintf(FILE *stream, const char *format [, argument, ...])  
`}</pre></figure>

							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
   FILE *fp;  
   fp = fopen("file.txt", "w");//opening file  
   fprintf(fp, "Hello file by fprintf...\\n");//writing data into file  
   fclose(fp);//closing file  
}  
`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Reading File : fscanf() function</h1>
							<p>The fscanf() function is used to read set of characters from file. It reads a word from the file and returns EOF at the end of file.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fscanf(FILE *stream, const char *format [, argument, ...]) `}</pre></figure>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
   FILE *fp;  
   char buff[255];//creating char array to store data of file  
   fp = fopen("file.txt", "r");  
   while(fscanf(fp, "%s", buff)!=EOF){  
   printf("%s ", buff );  
   }  
   fclose(fp);  
}  

Output:

Hello file by fprintf...`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Writing File : fputc() function</h1>
							<p>The fputc() function is used to write a single character into file. It outputs a character to a stream.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fputc(int c, FILE *stream)  	`}</pre></figure>

							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
   FILE *fp;  
   fp = fopen("file1.txt", "w");//opening file  
   fputc('a',fp);//writing single character into file  
   fclose(fp);//closing file  
}  

Output:

a`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Reading File : fgetc() function</h1>
							<p>The fgetc() function returns a single character from the file. It gets a character from the stream. It returns EOF at the end of file.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fgetc(FILE *stream)  `}</pre></figure>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
	FILE *fp;  
	char c;  
	clrscr();  
	fp=fopen("myfile.txt","r");  
	  
	while((c=fgetc(fp))!=EOF){  
	printf("%c",c);  
	}  
	fclose(fp);  
}  

Output:

this is simple text message`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Writing File : fputs() function</h1>
							<p>The fputs() function writes a line of characters into file. It outputs string to a stream.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fputs(const char *s, FILE *stream)  `}</pre></figure>

							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
	FILE *fp;  
	clrscr();  
	fp=fopen("myfile2.txt","w");  
	fputs("hello c programming",fp);  
	fclose(fp);  
}  

Output:

hello c programming`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">Reading File : fgets() function</h1>
							<p>The fgets() function reads a line of characters from file. It gets string from a stream.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`char* fgets(char *s, int n, FILE *stream)  `}</pre></figure>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
	FILE *fp;  
	char text[300];  
	clrscr();  
	fp=fopen("myfile2.txt","r");  
	printf("%s",fgets(text,200,fp));  
	fclose(fp);  
	getch();  
}  

Output:

hello c programming`}</pre></figure>
							<hr className="my-5" />
							<h1 className="mb-2 mt-3">C fseek() function</h1>
							<p>The fseek() function is used to set the file pointer to the specified offset. It is used to write data into file at desired location.</p>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
									{`int fseek(FILE *stream, long int offset, int whence)  `}
								</pre></figure>
							<figure className="code-block">
								<pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
									{`#include<stdio.h>
#include<conio.h> 
void main() 						
{ 
   FILE *fp;  
   fp = fopen("myfile.txt","w+");  
   fputs("This is javatpoint", fp);  
   fseek( fp, 7, SEEK_SET );  
   fputs("sonoo jaiswal", fp);  
   fclose(fp);  
}  

Output:

This is aman jain`}</pre></figure>
							<hr className="my-5" />
							<Footer />
						</article>

						<Aside />
					</div>
				</div>
			</main>
		</div>
	)
}