import React, { useEffect, useState } from "react";
import { useNavigate, Link } from "react-router-dom";
import Sidebar from "../Include/Sidebar";
import Footer from "../Include/Footer";
import Aside from "../Include/Aside";
import '../Styles/GlobalStyles';

export default function PhpVariable() {
    const navigate = useNavigate();
    const [email, setEmail] = useState("");
    const [sidebarToggled, setSidebarToggled] = useState(false);
    const [sidebarPinned, setSidebarPinned] = useState(false);

    useEffect(() => {
        const storedEmail = sessionStorage.getItem("userEmail");
        if (!storedEmail) {
            navigate("/");
        } else {
            setEmail(storedEmail);
        }
    }, [navigate]);

    const handleLogout = () => {
        sessionStorage.removeItem("userEmail");
        navigate("/");
    };

    const toggleSidebar = () => setSidebarToggled(!sidebarToggled);
    const togglePinSidebar = () => setSidebarPinned(!sidebarPinned);

    return (
        <div className={`page-wrapper light-theme ${sidebarToggled ? "toggled" : ""} ${sidebarPinned ? "pinned" : ""}`}>
            <Sidebar sidebarPinned={sidebarPinned} togglePinSidebar={togglePinSidebar} />

            <main className="page-content">
                {sidebarToggled && <div id="overlay" className="overlay" onClick={toggleSidebar}></div>}

                <div className="container-fluid">
                    {/* Top Bar */}
                    <div className="row d-flex align-items-center p-3 border-bottom">
                        <div className="col-md-1">
                            <button id="toggle-sidebar" className="btn rounded-0 p-3" onClick={toggleSidebar}>
                                <i className="fas fa-bars"></i>
                            </button>
                        </div>
                        <div className="col-md-8">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb d-none d-lg-inline-flex m-0">
                                    <li className="breadcrumb-item">
                                        <Link to="/dashboard">Home</Link>
                                    </li>
                                    <li className="breadcrumb-item active" aria-current="page">
                                        Variables
                                    </li>
                                </ol>
                            </nav>
                        </div>
                        <div className="col-md-3 text-right">
                            <button onClick={handleLogout} className="btn btn-link text-dark fw-semibold">
                                {email ? `${email} (Logout)` : "Logout"}
                            </button>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="row p-lg-4">
                        <article className="main-content col-md-9 pr-lg-5">

                            <h1 className="mb-2 mt-3">Variable</h1>

                            <p className="lead mb-3">The scope of a variable is defined as its range in the program under which it can be accessed. In other words, "The scope of a variable is the portion of the program within which it is defined and can be accessed." A variable is declared using a $ sign followed by the variable name. Here, some important points to know about variables:</p>
                            <ol>
                                <li>As PHP is a loosely typed language, so we do not need to declare the data types of the variables. It automatically analyzes the values and makes conversions to its correct datatype.</li>
                                <li>After declaring a variable, it can be reused throughout the code.</li>
                                <li>Assignment Operator (=) is used to assign the value to a variable.</li>
                            </ol>
                            <p>PHP has three types of variable scopes:</p>
                            <ol>
                                <li>Local variable</li>
                                <li>Global variable</li>
                                <li>Static variable</li>
                            </ol>

                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Syntax -->`}</div>
                                    {`$variablename=value;  
                                    `}
                                </pre></figure>
                            <hr className="my-5" />
                            <h3 className="font-weight-bold mb-2">
                                Rules for declaring PHP variable:
                            </h3>
                            <ul>
                                <li>A variable must start with a dollar ($) sign, followed by the variable name.</li>
                                <li>It can only contain alpha-numeric character and underscore (A-z, 0-9, _).</li>
                                <li>A variable name must start with a letter or underscore (_) character.</li>
                                <li>A PHP variable name cannot contain spaces.</li>
                                <li>One thing to be kept in mind that the variable name cannot start with a number or special symbols.</li>
                                <li>PHP variables are case-sensitive, so $name and $NAME both are treated as different variable.</li>
                            </ul>
                            <hr className="my-5" />
                            <p>Let's see the example to store string, integer, and float values in PHP variables.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$str="hello string";  
$x=200;  
$y=44.6;  
echo "string is: $str <br/>";  
echo "integer is: $x <br/>";  
echo "float is: $y <br/>";  
?>

Output:

string is: hello string
integer is: 200
float is: 44.6 `}</pre></figure>
                            <hr className="my-5" />
                            <h1 className="mb-2 mt-3">Local variable</h1>
						    <p>The variables that are declared within a function are called local variables for that function. These local variables have their scope only in that particular function in which they are declared. This means that these variables cannot be accessed outside the function, as they have local scope.</p>
                            <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
function local_var()  
{  
    $num = 45;  //local variable  
    echo "Local variable declared inside the function is: ". $num;  
}  
local_var();  
?>

Output:

Local variable declared inside the function is: 45`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Global variable</h1>
						<p>The global variables are the variables that are declared outside the function. These variables can be accessed anywhere in the program. To access the global variable within a function, use the GLOBAL keyword before the variable. However, these variables can be directly accessed or used outside the function without any keyword. Therefore there is no need to use any keyword to access a global variable outside the function.</p>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
$num1 = 5;      //global variable  
$num2 = 13;     //global variable  
function global_var()  
{  
    $sum = $GLOBALS['num1'] + $GLOBALS['num2'];  
    echo "Sum of global variables is: " .$sum;  
}  
global_var();  
?>

Output:

Sum of global variables is: 18`}</pre></figure>
<hr className="my-5"/>
						<h1 className="mb-2 mt-3">Static variable</h1>
						<p>It is a feature of PHP to delete the variable, once it completes its execution and memory is freed. Sometimes we need to store a variable even after completion of function execution. Therefore, another important feature of variable scoping is static variable. We use the static keyword before the variable to define a variable, and this variable is called as static variable.</p>
						<p>Static variables exist only in a local function, but it does not free its memory after the program execution leaves the scope. </p>
                        <figure className="code-block">
                                <pre><div className="text-muted mb-2">{`<!-- Example -->`}</div>
                                    {`<?php  
function static_var()  
{  
    static $num1 = 3;       //static variable  
    $num2 = 6;          //Non-static variable  
    //increment in non-static variable  
    $num1++;  
    //increment in static variable  
    $num2++;  
    echo "Static: " .$num1 ."</br>";  
    echo "Non-static: " .$num2 ."</br>";  
}  

static_var();  
static_var();  
?>

Output:

Static: 4
Non-static: 7
Static: 5
Non-static: 7`}</pre></figure>
                            <hr className="my-5" />
                            <Footer />
                        </article>

                        <Aside />
                    </div>
                </div>
            </main>
        </div>
    )
}
